import request from '../untils/request'

// 文章详情
export function detailGet(params) {
  return request({
    url: '/api/posts/detail',
    method: 'get',
    params,
  })
}


// 文章列表
export function listGet(params) {
  return request({
    url: '/api/posts/list',
    method: 'get',
    params,
  })
}


// 轮播图

export function advertGet(parmas) {
  return request({
    url: '/api/posts/advert',
    method: 'get',
    parmas,
  })
}