import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'
import {
  Row,
  Col
} from 'react-bootstrap'
import { Link } from 'react-router-dom'
import classNames from 'classnames'
import { ProductsListGet, fetchOrderList, fetchProductsDetail } from '../../../actions/product'

export class index extends Component {
  constructor(props) {
    super(props)
    this.state = {
      courseTipOneClose: true,
      orderList: [],
      moreProduct: [],
      proDetail: {
        name: "",
        thumb: "",
        description: ""
      },
    }

    this.handleClose = this.handleClose.bind(this);
  }

  componentDidMount() {
    const { dispatch } = this.props;
    dispatch(ProductsListGet())
      .then((data) => {
        this.setState({
          moreProduct: data.products,
        })
      })
    dispatch(fetchOrderList())
      .then((data) => {
        // 获取用户购买课程列表
        this.setState({
          orderList: data.orders,
        })
      })
  }

  handleDetails(num) {
    const { dispatch } = this.props;
    dispatch(fetchProductsDetail(num))
      .then((data) => {
        this.setState({
          proDetail: data,
        })
      })
    this.setState({
      courseTipOneClose: false,
    })
  }

  handleClose() {
    this.setState({
      courseTipOneClose: true,
    })
  }

  render() {
    const { courseTipOneClose, moreProduct, proDetail, orderList } = this.state;
    return (
      <Fragment>
        <div className="courseCenter">
          <div className="">
            <div className="groupTitle">我的课程</div>
            <div className="myCourse">
              <Row>
                {
                  orderList.length ? orderList.map((item, index) => {
                    return (
                      <Col md={6} key={index}>
                        <div className="Subscribed">
                          <img className="img-responsive" src={item.product.thumb} alt="" />
                          <div className="courseBtm">
                            <span className="courseName">{item.product.name}</span>
                            <Link target="_blank" to={"/download/"+ item.product.id} className="download">下载资源</Link>
                          </div>
                        </div>
                      </Col>
                    )
                  }) : (
                  <div className="noSub">
                    <p>
                      您还没有订阅课程。
                    </p>
                    <p>
                      您可以浏览下方的课程信息，或者联系我们了解更多课程信息。
                    </p>
                  </div>)
                }
              </Row>
            </div>
          </div>
          <div className={classNames("shade", { 'hidden': courseTipOneClose })}>
            <div className='courseDetail'>
              <img className="img-responsive" src={proDetail.thumb || require('../../../images/personal/course01.png')} alt="" />
              <div className="main">
                <h2>{proDetail.name}</h2>
                <p className="prov">课程提供：校萌教育链</p>
                <p className="desc" dangerouslySetInnerHTML={{__html: proDetail.description}}></p>
                <div className="closeWrap">
                  <p>订阅课程或了解更多课程信息，请联系</p>
                  <p>0756-2225520</p>
                  <button onClick={this.handleClose}>关闭</button>
                </div>
              </div>
            </div>
          </div>
          <div className="">
            <div className="groupTitle">更多课程</div>
            <div className="moreCourse">
              <Row>
                {
                  moreProduct.length ? moreProduct.map((item, ind) => {
                    return (
                      <Col md={6} key={ind}>
                        <div className="courseItem">
                          <img className="img-responsive" src={item.thumb || require("../../../images/personal/course01.png")} alt="" />
                          <div className="courseBtm">
                            <span className="courseName">{item.name}</span>
                            <button onClick={this.handleDetails.bind(this, item.id)} className="details">查看详情</button>
                          </div>
                        </div>
                      </Col>
                    )
                  }) : "nothing"
                }
              </Row>
            </div>
          </div>
        </div>
      </Fragment>
    )
  }
}

const mapStateToProps = (state) => ({

})



export default connect(mapStateToProps)(index)
