import React, { Component } from 'react'
import { Carousel } from 'react-bootstrap';

export default class School extends Component {
  render() {
    return (
      <Carousel>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_01">
            <div className="caption onRight big">
              <h2>预备学校（第 1－8 班）</h2>
              <p>英国学制的第 1－8 班相等于中国的小一至初二，称为「预备学校」(Preparatory School，简称 Prep School)，不少均设有「一条龙」或关连中学。校方评核入学申请时，除了考虑学业成绩，还注重其他范畴的表现，如音乐、体育及其他课外活动。部分顶尖学校如 Cheltenham College 等更会要求报读生到校留宿一晚，以便全面了解学生性格及长短。根据经验，愈早到英国升学，学校选择便愈多，学生亦有更大机会入读较高名气的寄宿学校。</p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_02">
            <div className="caption onRight middle">
              <h2>中学（第 9－13 班）</h2>
              <p>英国中学学制的第 9 班是不少中学的主要收生年，学生于第 10－11 班完成会考课程，并于第 12－13 班完成高考，考入当地以至全球专上学府。</p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_03">
            <div className="caption onRight big">
              <h2>英国会考课程（GCSE）</h2>
              <p>英国会考课程（GCSE）课程为期两年，于第 10－11 班完成，学生一般修读 7－10 科，选科并无文理科限制，学习更为自由。部分学校设有 IGCSE，课程提纲跟 GCSE 相若，但校内评核比重较轻。考试评分等级分为 A*-G，合格标准为 A*-C，但 GCSE 近年正在改制，部分学科成绩评级会由传统的 A*-G 级改为 1－9 级，9 级为最佳。</p>
            </div>
          </div> 
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_04">
            <div className="caption onLeft big">
                <h2>英国高考课程（A-Level）</h2>
                <p>
                  英国高考课程（A-Level）为期两年，于第 12－13 班完成，为升读大学的主要途径。课程并无文理科的选科限制，学生通常修读 3－4 科，学生可根据自己的升学意向选科，在高考中取得三科合格便符合升读英国大学资格。
                </p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_05">
            <div className="caption onLeft big">
              <h2>IB 预科课程 <br/>
              &nbsp;&nbsp;(IB Diploma Program)
              </h2>
              <p>国际文凭课程 (IB) 是一个横跨小学、初中及高中的课程。国际文凭预科课程（IB）为期两年，获世界各地的大学认可，是英国高考课程（A-Level）以外的另一选择，于第 12－13 班完成。相比英国高考，此课程的内容更广泛，学生除了要修读六个不同范畴的科目，及完成延伸论文等核心科目，学生须具备良好的阅读与写作能力，而香港亦有国际学校采用此国际文凭课程。</p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_06">
            <div className="caption onRight big">
              <h2>BTEC 英国国家文凭课程<br/>
              &nbsp;&nbsp;(BTEC National Diploma)
              </h2>
              <p>BTEC 英国国家文凭课程分为 1－7 级，第 3 级的 BTEC 课程为期两年，相当于英国高考课程（A-Level）的学历，超过 200 间国际大学认可，包括香港大部分的大学。课程注重传授实务知识及技能，科目包括商业、设计及旅游等，有别于传统学科。评估方式以习作及专题研习为主，考试占重较少。学生完成课程后可获 UCAS 分数，来报读大学或就业。</p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_07">
            <div className="caption onLeft big">
                <h2>剑桥大学文凭课程<br/>
                &nbsp;&nbsp;（Cambridge Pre-U）
                </h2>
                <p>剑桥大学预科课程（Cambridge Pre-U）为期两年，为剑桥大学国际考试局设立的新学历课程，于 2008 年正式在个别英国学校内施教。课程注重学生的高层次思维，结构与英国高考（A-Level）相似，选科更具弹性，评估方式多元，要求十分严格。学生一般从 27 个主修科中选读 3－4 科，也可跟英国高考（A-Level）合并修读，例如两科 Pre-U 加一科 A-Level，用合并的成绩一同报读大学。除主修科外，学生须完成 Global Perspectives and Research (GPR)。课程现时已获多家高等学府认可，包括剑桥大学及牛津大学。</p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_08">
            <div className="caption onLeft big">
              <h2>大学基础课程<br/>
              &nbsp;&nbsp;（Foundation Programme）
              </h2>
              <p>大学基础课程为期一年，适合完成中国中学课程的毕业生修读，此课程的学习模式与大学相同，为学生入读学士课程作好准备。课程可分为两类：第一类是与个别大学联系的预备班（University Foundation Programme），成绩达标者可升读联系大学的一年级；另一类是由独立院校提供的国际预备班（International Foundation Programme），学生可凭校内成绩报读多所大学。</p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_09">
            <div className="caption onRight big">
              <h2>大学学士课程<br/>
              &nbsp;&nbsp; (Bachelor Degrees)</h2>
              <p>在英格兰、威尔斯及爱尔兰地区，大学学制一般为三年，若课程包含企业实习、海外交换学生计划，则会延长至四年，一般为第三年实习／交换，第四年回到学校完成学业。而一些特定科系如医科、建筑等，修业年限则较长。苏格兰地区学制则和英国其他地区较不同，一般为四年制大学课程</p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_10">
            <div className="caption onRight big">
              <h2>大学学士课程 <br/>
              &nbsp;&nbsp;(Bachelor Degrees)</h2>
              <p>英国大学评分为等第制，学位的授与会因修课内容及成绩而有所分别，由高而低分别是： <br/>
                ● First class honours (First or 1st) <br/>
                ● Upper second class honours (2:1 or 2i) <br/>
                ● Lower second class honours (2:2 or 2ii) <br/>
                ● Third class honours (Third or 3rd) <br/>
                ● Pass  <br/>
                若成绩低于第五等第，则为Fail，无法顺利取得学士学位。</p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_11">
            <div className="caption onRight big">
              <h2>英国硕士学位 <br/>
              &nbsp;&nbsp;(Master Degree)</h2>
              <p>英国向来以高质量教育水平闻名，学术研究竞争力在全球向来也是数一数二，再加上英国硕士学制短，密集式课程通常只需一年，因此吸引许多国际学生前往英国留学。</p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_12">
            <div className="caption onRight big">
              <h2>英国硕士学位<br/>
              &nbsp;&nbsp; (Master Degree)</h2>
              <p>证书及文凭课程 (Diploma/Certificate) 证书及文凭课程通常为期9个月，实际的课程长度会依学校/科系之课程的设计而有所不同。此类课程设计以授课式教学为主，并结合分组讨论、指导教学和个人自习等上课方式。证书和文凭课程的特点之一，在于其对于入学门槛的要求，通常较不如正式的硕士课程严格。此外，证书及文凭课程与正式硕士课程的最大差异，在于前者无须缴交毕业论文，而教学方式和评量标准则大致相同。</p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_13">
            <div className="caption onRight big">
              <h2>英国硕士学位<br/>
              &nbsp;&nbsp; (Master Degree)</h2>
              <p>授课式硕士 (Taught Master) 授课式硕士课程通常为期12个月，大多数台湾学生至英国留学都是选读此类课程。前两个学期主要结合授课、分组讨论、指导教学和个人自习等上课方式，最后一学期则专注于进行研究、撰写毕业论文或研究报告。</p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item xuezhi_14">
            <div className="caption onLeft">
              <h2>英国硕士学位<br/>
              &nbsp;&nbsp; (Master Degree)</h2>
              <p>研究式硕士 (Research Master) 研究式硕士课程通常为期1至3年，学生不须修课，只要为个人与教授之间的研究与讨论，毕业时缴交论文。若修业期间表现优异并获指导教授肯定，则可申请继续进行博士研究，而在硕士期间所进行的研究亦会被视为博士研究的一部分。</p>
            </div>
          </div>
        </Carousel.Item>
      </Carousel>
    )
  }
}
