import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import { Col,Row } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
 
import NavBar from '../../Components/NavBar/NavBar';
import Climate from '../../Components/BannerList/Climate/Climate';
import History from '../../Components/BannerList/History/History';
import School from '../../Components/BannerList/School/School';
import Life from '../../Components/BannerList/Life/Life';


import './index.css';

export default class LenUkDetail extends Component {
  render() {
    return (
      <div className="LenUkDetail-wrapper">
        <NavBar backgroundColor="#fff" boxShadow="0 0 10px #ccc"/> 
        <div className="LenUkDetail-body">
          <Route exact path="/realize/bannerlist" component={Climate}></Route>
          <Route exact path="/realize/bannerlist/history" component={History}></Route>
          <Route exact path="/realize/bannerlist/school" component={School}></Route>
          <Route exact path="/realize/bannerlist/life" component={Life}></Route>
        </div>
        <Row className="navlist-btm">
           <Col md={10} mdOffset={1}>
            <ul className="navlist-btm-items row">
              <li className="col-md-3"><NavLink exact to="/realize/bannerlist" activeClassName="nav-btn-active">英伦气候</NavLink></li>
              <li className="col-md-3"><NavLink to="/realize/bannerlist/history" activeClassName="nav-btn-active">英伦史地</NavLink></li>
              <li className="col-md-3"><NavLink to="/realize/bannerlist/school" activeClassName="nav-btn-active">英伦学制</NavLink></li>
              <li className="col-md-3"><NavLink to="/realize/bannerlist/life" activeClassName="nav-btn-active">英伦生活</NavLink></li>
            </ul>
          </Col>
        </Row>
      </div>
      
    )
  }
}
