const emailReg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/
const phoneReg = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/

export const validateEmail = function(val) {
  return emailReg.test(val);
} 


export const validatePhone = function(val) {
  return phoneReg.test(val);
}

export const validateLength = function(max, min, val) {
  const len = val.length;
  if(max === min) {
    return ( len === max);
  } else {
    return ( len < max && len>min );
  }
}

