import request from '../untils/request'

// 获取二维码 
export function doLoginGet() {
  return request({
    url: `/api/auth/login`,
    method: 'get'
  })
}

// 获取用户信息
export function doLoginPost(data) {
  return request({
    url: `/api/auth/login`,
    method: 'post',
    data,
  })
}

// 退出
export function logout() {
  return request({
    url: `api/auth/logout`,
    method: "get",
  })
}
