import Lockr from 'lockr'

export function setToken(token) {
  Lockr.set('token', token);
}

export function getToken() {
  return Lockr.get('token');
}

export function removeToken() {
  Lockr.set('token');
}

export function setApiToken(token) {
  Lockr.set('api_token', token);
}

export function getApiToken() {
  return Lockr.get('api_token')
}

export function removeApiToken() {
  Lockr.set('api_token')
}


export function setUserInfo(info) {
  Lockr.set('userInfo',info)
}

export function getUserInfo(info) {
  Lockr.get('userInfo')
}

export function removeUserinfo(info) {
  Lockr.set('userInfo')
}

export function setBaseInfo(info) {
  Lockr.set('baseInfo', info)
}

export function getBaseInfo() {
  Lockr.get('baseInfo')
}

export function removeBaseInfo(){
  Lockr.set('userInfo')
}