import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import NavBar from '../../Components/NavBar/NavBar';
import { Row,Col } from 'react-bootstrap';
import { connect } from 'react-redux'
import { getPostDetail } from '../../actions/post'
import { getApiToken } from '../../untils/auth'

import './index.css';


class Post extends Component {
  constructor(props) {
    super(props);
    this.state = {
      postDetail: {
        prePost: {
          id: '',
          title: '',
        },
        nextPost: {
          id: '',
          title: '',
        }
      },
    }
  }

  componentDidMount() {
    window.scrollTo(0,0);
    this.props.dispatch(getPostDetail({
      postId: this.props.match.params.id,
      category: 11,
      api_token: getApiToken()
    })).then(data => {
      console.log("post",data)
      this.setState({
        postDetail: data,
      })
    })
  }

  render() {
    const { postDetail } = this.state;
    return (
      <div className="Post-wrapper">
        <NavBar backgroundColor="#fff" boxShadow="0 0 10px #ccc" />
        <div className="Post-body" style={{
          backgroundImage: `url(${postDetail.thumb})`,
          backgroundSize: "cover",
          backgroundRepeat: "no-repeat",
          height: "5rem",
        }}>
          {/* <img className="post-header" src={postDetail.thumb} alt="文章图片"/> */}
        </div>
        <Row className="mixMargin">
          <Col md={10} mdOffset={1}>
            <div className="post-content">
              <div className="title">{postDetail.title}</div>
              <div className="credit_at">{postDetail.created_at}</div>
              <div className="contBody" dangerouslySetInnerHTML={{__html: postDetail.content}}>
              </div>
              <div className="pageList">
                {postDetail.prePost ? <div id="pageUp"><span>上一篇</span><a title={postDetail.prePost.title} href={'/aboutus/post/' + postDetail.prePost.id}>{postDetail.prePost.title}</a></div> : ''}
                {postDetail.nextPost ? <div id="pageDown"><a title={postDetail.nextPost.title} href={'/aboutus/post/' + postDetail.nextPost.id}>{postDetail.nextPost.title}</a><span>下一篇</span></div> : ""}               
              </div>
            </div>
            <div className="returnback"> <Link to="/aboutus">关闭窗口</Link></div>
          </Col>
        </Row>
        
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {

  }
}

export default connect(mapStateToProps)(Post);
