import React, { Component } from 'react'
import { connect } from 'react-redux'
import {
  Row,
  Col
} from 'react-bootstrap'
import { Select } from 'antd';
import classNames from 'classnames'
import { validateEmail, validatePhone } from '../../../untils/validate'
import {
  getBaseInfo,
  setBasicInfo,
  getapiFilesList
} from '../../../actions/user'
import Upload from 'rc-upload'
import { getApiToken } from '../../../untils/auth'
import { message } from 'antd';

const Option = Select.Option;

class index extends Component {
  componentDidMount() {
    console.log(this.props)
    const { dispatch } = this.props
    const that = this;
    // 获取基础信息
    dispatch(getBaseInfo())
      .then((data) => {
        console.log(data);
        that.setState((state) => {
          return {
            formData: {
              ...state.formData,
              ...data,
            }
          }
        })
      })
    // 获取需要上传的基础文件
    dispatch(getapiFilesList())
      .then(data => {
        console.log(data);
        this.setState({
          fileList: [...data],
        })
      })
  }
  constructor(props) {
    super(props);
    this.state = {
      formData: {
        nickname: "",
        name: "",
        sex: "",
        email: "",
        phone: "",
        recode: "",
        invite_code: "",
        application_type: "",
      },
      formRules: {
        name: false,
        email: false,
        phone: false,
        recode: "",
        sex: false,
      },
      fileList: [],
      avator: "",
      inviteCode: "",
      headers: {
        toekn: getApiToken(),
      },

    }
    this.changeSex = this.changeSex.bind(this);
    this.changeEmail = this.changeEmail.bind(this);
    this.changePhone = this.changePhone.bind(this);
    this.changeName = this.changeName.bind(this);
    this.depositForm = this.depositForm.bind(this);
    this.validateName = this.validateName.bind(this);
    this.validateSex = this.validateSex.bind(this);
    this.fileSuccess = this.fileSuccess.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  // 修改表单
  depositForm() {

    const { dispatch } = this.props;
    const { formData } = this.state
    const Data = {
      phone: formData.phone,
      sex: formData.sex,
      name: formData.name,
      email: formData.email,
      application_type: formData.application_type,
    }
    // 进行表单校验
    // if(!(formData.phone && (formData.sex !== '') && formData.name && formData.email)) {
    //   message.error("请完善个人资料！")
    // } else if (!formData.application_type) {
    //   message.error("请填写升学类型！")
    // } else if (formData.name<2) {
    //   message.error("请填写正确的姓名！")
    // } else if (formData.sex === '') {
    //   message.error("请填写性别！")
    // } else if (!validateEmail(formData.email)) {
    //   message.error("邮箱格式错误")
    // } else if (!validatePhone(formData.phone)) {
    //   message.error("请填写正确的手机号码")
    // } else {

    // }

    dispatch(setBasicInfo(Data)).then(() => {
      message.success("保存成功")
    })




  }

  // 校验规则
  validateName() {
    return this.state.formData.name.length >= 2;
  }
  // 检验性别
  validateSex() {
    return this.state.formData.sex !== ''
  }


  changeName(e) {
    const formData = Object.assign({}, this.state.formData, { name: e.target.value })
    this.setState({
      formData,
      formRules: {
        ...this.state.formRules,
        name: e.target.value.length > 2,
      }
    })
  }

  changeSex(e) {
    const formData = Object.assign({}, this.state.formData, { sex: e })
    this.setState({
      formData,
      formRules: {
        ...this.state.formRules,
        sex: true,
      }
    }, function () {
      this.depositForm()
    })

  }

  changeEmail(e) {
    const val = e.target.value
    const formData = Object.assign({}, this.state.formData, { email: val })
    this.setState({
      formData,
    })
    if (validateEmail(val)) {
      this.setState({
        formRules: {
          ...this.state.formRules,
          email: true,
        }
      })
    } else {
      this.setState({
        formRules: {
          ...this.state.formRules,
          email: false,
        }
      })
    }
  }

  changePhone(e) {
    const formData = Object.assign({}, this.state.formData, { phone: e.target.value })
    this.setState({
      formData,
    })
    if (validatePhone(e.target.value)) {
      this.setState({
        formRules: {
          ...this.state.formRules,
          phone: true,
        }
      })
    } else {
      this.setState({
        formRules: {
          ...this.state.formRules,
          phone: false,
        }
      })
    }
  }

  fileSuccess(e) {
    switch (e.code) {
      case "3000":
        message.error("文件上传失败-不允许的文件格式")
        break;
      case "3001":
        message.error("文件上传失败-文件大小超过限制")
        break;
      case "30002":
        message.error("文件上传失败-没有该文件记录")
        break;
      default:
        message.success("文件上传成功")
        this.props.dispatch(getapiFilesList())
          .then(data => {
            this.setState({
              fileList: [...data],
            })
          })

        this.props.dispatch(getBaseInfo())
        break;
    }
  }

  // 选择留学类型
  handleChange(val) {
    this.setState((state) => {
      return {
        formData: {
          ...state.formData,
          application_type: val,
        }
      }
    }, function () {
      this.depositForm();
    })

  }
  // 限制上传文件大小
  beforeUpload(allow_exts, max_size, file) {
    console.log(file.name)
    console.log(allow_exts)
    console.log(max_size)
    const isLt10 = file.size / 1024 / 1024 < max_size;
    if (!isLt10) {
      message.error("文件大小不能超过"+max_size+"M");
    }
    let isRight = false;
    var arr = allow_exts.split(",");

    for(var i = 0;i<arr.length;i++) {
      if(file.name.indexOf(arr[i]) != -1) {
        isRight = true;
        break;
      }
    }

    // const isRight = (file.type === 'image/jpeg')
    //   || (file.type === 'image/jpg')
    //   || (file.type === 'image/png')
    //   || (file.type === 'aplication/zip')
    //   || (file.type === 'application/x-zip-compressed')
    //   || (/.rar$/.test(file.name))

    if (!isRight) {
      message.error("请上传"+allow_exts+"格式的文件")
    }
    return isLt10 && isRight;
  }



  render() {
    const { email } = this.state.formData
    const { baseInfo } = this.props
    function d(val) {
      return val.substr(0, 10)
    }
    return (
      <div>
        <div className="personalInfo">
          <div className="formTitle">注册信息</div>
          <div className="Registration">
            <div className="inputGroup">
              <label htmlFor="nickname">昵称</label>
              <input type="text" readOnly value={this.state.formData.nickname} />
            </div>
            <div className="inputGroup">
              <label htmlFor="">头像</label>
              <Upload
                multiple={true}
                data={(file) => {
                  return {
                    token: getApiToken(),
                  }
                }}
                onSuccess={this.fileSuccess}
                action="/api/user/avatar" >
                <img className="userIcon" alt="" src={baseInfo.avatar || require('../../../images/personal/toux.png')} />
              </Upload>
            </div>
            <div className="inputGroup">
              <label htmlFor="">绑定微信</label>
              <span className="wx">{this.state.formData.nickname}</span>
            </div>
            <div className="inputGroup">
              <label htmlFor="">邀请码</label>
              <input type="text" readOnly value={this.state.formData.invite_code} />
            </div>
            <div className="inputGroup">
              <label htmlFor="">升学类型</label>
              <Select value={this.state.formData.application_type} style={{ width: "5.7rem", borderColor: "#00A5D7" }} onChange={this.handleChange}>
                <Option value={0}>中学</Option>
                <Option value={1}>本科</Option>
                <Option value={2}>研究生</Option>
              </Select>
            </div>
          </div>
          <div className="formTitle">个人资料（以下信息会用于升学申请，请确保信息准确）</div>
          <div className="personalInfo">
            <div className="inputGroup">
              <label htmlFor="">姓名</label>
              <input type="text" value={this.state.formData.name} onBlur={this.depositForm} onChange={this.changeName} />
              <div className={classNames({ "inputState": this.validateName() })}></div>
            </div>
            <div className="inputGroup">
              <label htmlFor="">性别</label>
              <Select value={this.state.formData.sex} style={{ width: "5.7rem", borderColor: "#00A5D7" }} onChange={this.changeSex}>
                <Option value={0}>男</Option>
                <Option value={1}>女</Option>
              </Select>
              <div className={classNames({ "inputState": this.validateSex() })}></div>
            </div>
            <div className="inputGroup">
              <label htmlFor="">常用邮箱</label>
              <input type="email" value={email} onChange={this.changeEmail} onBlur={this.depositForm} />
              <div className={classNames({ "inputState": validateEmail(this.state.formData.email) })}></div>
            </div>
            <div className="inputGroup">
              <label htmlFor="">手机号码</label>
              <input type="text" value={this.state.formData.phone} onChange={this.changePhone} onBlur={this.depositForm} />
              <div className={classNames({ "inputState": validatePhone(this.state.formData.phone) })}></div>
            </div>
            <div className="inputGroup">
              <label>证明材料</label>
              <Row>
                {
                  this.state.fileList.map((item, ind) => {
                    return (
                      <Col md={4} key={ind}>
                        <div className="uploadItem">
                          <div className="left">
                            <h3>{item.name}</h3>
                            <p>文件格式：{item.allow_exts}</p>
                            <p style={item.uploaded_at ? {} : { visibility: "hidden" }}>上传时间：{item.uploaded_at && d(item.uploaded_at)}</p>
                          </div>
                          <div className="right">
                            <Upload
                              multiple={true}
                              data={(file) => {
                                return {
                                  fileId: item.id,
                                  token: getApiToken(),
                                }
                              }}
                              beforeUpload={this.beforeUpload.bind(this,item.allow_exts,item.max_size)}
                              onSuccess={this.fileSuccess}
                              action="/api/user/files/upload" >
                              {!!item.uploaded_at ? <img className="img-responsive" src={require('../../../images/personal/reupload.png')} alt="" /> : <img className="img-responsive" src={require('../../../images/personal/upload.png')} alt="" />}
                            </Upload>
                          </div>
                          <div className={classNames({ "state": !!item.uploaded_at })}></div>
                        </div>
                      </Col>
                    )
                  })

                }
              </Row>
            </div>
            <p>温馨提示：证明文件需为扫描件，请使用扫描仪或文档扫描类APP进行扫描，以确保清晰度。</p>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  baseInfo: state.baseInfo,
})


export default connect(mapStateToProps)(index)
