import React, { Component } from 'react';
import { withRouter } from 'react-router';
import './index.css';

class Notifice extends Component {
  constructor(props) {
    super(props);
    this.state = {
      changeState: false,
      title: "",
    }
  }

  componentWillMount() {
    const { title } = this.props;
    this.setState({
      title,
    })
  }

  componentWillReceiveProps (nextProps) {
    const { title } = nextProps;
    this.setState({
      title,
    })
  }

  change() {
    window.setTimeout(() => {
      this.setState((state)=>({
        changeState: !state.changeState,
      }))
    },5000);
  }
  componentDidMount() {
    this.change();
  }

  // methods
  goDetail(id) {
    this.props.history.push("/aboutus/post/" + id)
  }

  render() {
    const { id } = this.props;
    const { title } = this.state;
    return (
      <div style={{ cursor: "pointer" }} onClick={this.goDetail.bind(this,id)} id="notifies" className={['Notifies-wrapper',this.state.changeState?'Notifies-wrapper-abnor':''].join(' ')}>
        <div className="noti-logo" style={{ display: this.state.changeState ? "none" : "inline-block"}}>
          <img src={`${require('../../images/noti-logo.png')}`} alt=""/>
        </div>
        { title }
      </div>
    )
  }
}


export default withRouter(Notifice)
