import React, { Component } from 'react'
import { Carousel } from 'react-bootstrap';

export default class History extends Component {
  render() {
    return (
      <Carousel>
        <Carousel.Item>
          <div className="bannder-img-item shidi_01">
            <div className="caption onLeft">
              <p>
                英国是欧洲西部岛国，位于不列颠群岛上，全称为大不列颠及北爱尔兰联合王国。它由英格兰、威尔斯、苏格兰和北爱尔兰组成，西临大西洋﹐东隔北海﹐南以多佛尔海峡和英吉利海峡同欧洲大陆相望﹐陆疆仅邻爱尔兰。面积24.4万平方公里（约中国的四十分之一），人口约6000万，而整个英国的历史也就是由这四个区域的历史交织组成。
              </p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item shidi_02">
            <div className="caption onRight big">
              <p>
                1535年威尔斯成为英格兰王国的一部分，1588年击败西班牙「无敌舰队」，树立海上霸权。1640年英国在全球第一个爆发资产阶级革命，成为资产阶级革命的先驱。1649年5月19日宣布成立共和国。1660年王朝复辟，1688年发生「光荣革命」，确定了君主立宪制。1707年英格兰与苏格兰合并，1801年又与爱尔兰合并。18世纪后半叶至19世纪上半叶，成为世界上第一个完成工业革命的国家。19世纪是大英帝国的全盛时期，1914年占有的殖民地比本土大111倍，是第一殖民大国，自称「日不落帝国」。1922年爱尔兰共和国独立，爱尔兰北部仍留在联合王国内。
              </p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item shidi_03">
            <div className="caption middle onRight">
              <p>
                英国民族是在长期历史过程中﹐经历多次民族迁移和混合逐渐形成的。较早的居民是公元前7世纪至前3世纪从南欧移入的克尔特人。公元 5～7 世纪﹐原住莱茵河流域的盎格鲁和萨克逊人移入东南部﹐把克尔特人排挤到西北部山地。其后丹麦人﹑诺曼人先后移入。诺曼人原住北欧﹐于11世纪后期进入英国前曾长期在法国西北部停留﹐语言﹑文化上有浓厚法兰西色彩。他们与盎格鲁﹑萨克逊人经几个世纪混合形成英格兰人。英语即以盎格鲁﹑萨克逊语为基础﹐吸收诺曼语和古法兰西语成分形成。
              </p>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="bannder-img-item shidi_04">
            <div className="caption onRight big">
              <p>
              现在英格兰人为多数﹐约占全国人口80％以上。苏格兰人﹑威尔士人和爱尔兰人分别占全国人口10％﹑2％和4％﹐他们皆系克尔特人﹐系少数民族。威尔士1284年起成为英国封邑﹐1535年并入英国﹔苏格兰原为独立国﹐1707年并入英格兰﹔北爱尔兰原是爱尔兰一部分﹐1921年爱尔兰南部26郡成立“自由邦” 时被划归英国。自此形成今日的“联合王国”。各少数民族受英格兰人长期影响﹐语言﹑文化差别愈来愈小﹐但仍保留一些传统习俗﹐他们集中居住地区经济发展水平明显偏低。
              </p>
            </div>
          </div>
        </Carousel.Item>
      </Carousel>
    )
  }
}
