import request from '../untils/request'

// 申请-可申请列表
export function applicationsListGet(params) {
  return request({
    url: `/api/applications/list`,
    method: 'get',
    params,
  })
}


// 用户-申请-我的申请

export function userApplicationListGet(params) {
  return request({
    url: `/api/user/application/list`,
    method: 'get',
    params
  })
}


// 申请进度-用户进度标记为已读

export function userApplicationProgresRead(data) {
  return request({
    url: `api/user/application/progres/read`,
    method: "post",
    data
  })
}


// 用户-已完成积分任务列表

export function userCreditMissionGet(params) {
  return request({
    url: `api/user/credit/mission`,
    method: "get",
    params,
  })
}

// 用户-已完成积分任务标记已读

export function userCreditMissionPost(data) {
  return request({
    url: `api/user/credit/mission`,
    method: "post",
    data
  })
}