import {
  baseInfoGet,
  baseInfoPost,
  creditLog,
  apiFilesList,
  FilesUpload,
  creditRulesGet,
  creditEnchashmentGet,
  userInvitees,
} from '../api/user'
import { getApiToken } from '../untils/auth'

export const baseInfoCreater = (info) => {
  return {
    type: "BASEINFO",
    baseinfo: info,
  }
}

export const getBaseInfo = () => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      baseInfoGet({ api_token: getApiToken() })
        .then((res) => {
          const data = res.data;
          if (data.code === "0000") {
            dispatch(baseInfoCreater(data.data))
            resolve(data.data)
          }
        })
    })
  }
}

export const setBasicInfo = (info) => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken(),
        phone_area: '86',
        ...info
      }
      baseInfoPost(data)
        .then(res => {
          const data = res.data;
          if (data.code === '0000') {
            resolve(data.data)
          }
        })
    })
  }
}

export const getCreditLog = (data) => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      creditLog(data)
        .then(res => {
          const data = res.data;
          if (data.code === '0000') {
            resolve(data.data)
          }
        })
    })
  }
}

export const getapiFilesList = (info) => {
  const data = {
    api_token: getApiToken(),
    ...info
  }
  return dispatch => {
    return new Promise((resolve, reject) => {
      apiFilesList(data)
        .then(res => {
          const data = res.data;
          if (data.code === '0000') {
            resolve(data.data)
          }
        })
    })
  }
}

export const doFilesUpload = () => {
  return new Promise((resolve, reject) => {
    FilesUpload()
      .then(res => {
        const data = res.data;
        if (data.code === '0000') {
          resolve(data.data)
        }
      })
  })
}


export const fetchCreditRules = () => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken(),
      }
      creditRulesGet(data)
        .then(res => {
          const data = res.data;
          if (data.code === '0000') {
            resolve(data.data)
          } else {
            reject();
          }
        })
    })
  }
}

export const fetchCreditEnchashmentGet = (total) => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken(),
        total,
      }
      creditEnchashmentGet(data)
        .then(res => {
          const data = res.data;
          if (data.code === "0000") {
            resolve(data)
          } else {
            reject(data.code)
          }
        })
    })
  }
}

export const fetchUserInvitees = (total) => { 
  return dispatch => {
    return new Promise((resolve, reject) => { 
      const data = {
        api_token: getApiToken(),
      }
      userInvitees(data)
        .then(res => {
          const data = res.data;
          if(data.code === "0000") {
            resolve(data.data)
          } else {
            reject(data.code);
          }
        })
    })
  }
}


