import React, { Component } from 'react'
import {
  Row,
  Col
} from 'react-bootstrap'

import { connect } from 'react-redux'

import Steps from 'rmc-steps'
import 'rmc-steps/assets/index.css'
import 'rmc-steps/assets/iconfont.css'
import classNames from 'classnames'
import Selfinfo from './SelfInfo'
import Products from './Products'
import './index.scss'
import 'react-perfect-scrollbar/dist/css/styles.css';
import PerfectScrollbar from 'react-perfect-scrollbar';

import { getApiToken } from '../../untils/auth'

import {
  getBaseInfo,
  getCreditLog, fetchCreditRules,
  fetchCreditEnchashmentGet,
  fetchUserInvitees,
} from '../../actions/user'
import { fetchUserApplicationsList, fetchUserApplicationProgresRead, fetchUserCreditMission, updateUserCreditMission } from '../../actions/application'
import { message } from 'antd';
import _ from 'lodash';
import accounting  from "accounting"

import { Pagination } from 'antd';


// 选项卡列表

const tabList = [
  '申请进度',
  '个人信息',
  '课程中心',
  '积分中心'
]





class index extends Component {

  constructor(props) {
    super(props);
    this.state = {
      pannelIdx: 0,
      eduIndex: 1,
      processIdx: 0,
      infoState: false,
      tokenHistoryClose: true,
      myInviteClose: true,
      tokenDepositClose: true,
      token: 0,
      creditLog: [],
      ruleList: [],
      inviteList: [],
      processList: [],
      mission: [],
      creditLogTotal: 0,
      creditLogCurPage: 1,
      isAppEmpty: true, //申请进度列表为空
    }

    this.handleClose = this.handleClose.bind(this);
    this.opentokenHistory = this.opentokenHistory.bind(this);
    this.openMyInvite = this.openMyInvite.bind(this);
    this.openToeknDeposit = this.openToeknDeposit.bind(this);
    this.changeToken = this.changeToken.bind(this);
    this.tokenConvert = this.tokenConvert.bind(this);
    this.handleTab = this.handleTab.bind(this);
    this.changeCreditLogSize = this.changeCreditLogSize.bind(this);
    this.closeCredit = this.closeCredit.bind(this);


    this.curImage = this.curImage.bind(this);
  }

  componentDidMount() {
    const { dispatch } = this.props;
    dispatch(fetchCreditRules())
      .then(data => {
        this.setState({
          ruleList: data,
        })
      })


    // 用户申请进度
    dispatch(fetchUserApplicationsList())
      .then((data) => {
        console.log("申请进度列表", data)
        if (data[0].user_application_id !== 0) {
          this.setState({
            isAppEmpty: false,
          }, function () {
            // 定时刷新进度
            setInterval(function () {
              dispatch(fetchUserApplicationsList())
                .then((data) => {
                  const nProcessList = data[0].progress;
                  if (!_.isEqual(this.state.processList, nProcessList)) {
                    console.log("dd", nProcessList)
                    for (let i = 0; i < nProcessList.length; i++) {
                      if (nProcessList[i].user_status === 2 && nProcessList[i].user_read === 0 && this.state.processIdx < data[0].progress.length - 1) {
                        dispatch(fetchUserApplicationProgresRead(nProcessList[i].user_progres_id))
                        setTimeout(() => {
                          this.setState((state) => {
                            console.log(state.processIdx + 1)
                            return {
                              processIdx: state.processIdx + 1
                            }
                          })
                        }, 1.5 * 1000);
                        // dispatch(fetchUserApplicationProgresRead(this.state.processList[i].user_progres_id))
                      }
                    }
                    this.setState({
                      processList: data[0].progress
                    })
                  }
                })
            }.bind(this), 10 * 1000);

          })
          const processList = data[0].progress
          for (let i = 0; i < data[0].progress.length; i++) {
            if (processList[i].user_status === 2 && processList[i].user_read === 0 && this.state.processIdx < data[0].progress.length - 1) {
              setTimeout(() => {
                this.setState((state) => {
                  return { processIdx: state.processIdx + 1 }
                })
              }, 2.5 * 1000);
              dispatch(fetchUserApplicationProgresRead(processList[i].user_progres_id))
            } else if (processList[i].user_status === 2 && processList[i].user_read === 1 && this.state.processIdx < data[0].progress.length - 1) {

              this.setState((state) => {
                return { processIdx: state.processIdx + 1 }
              })

            }
          }
          this.setState({
            processList: data[0].progress
          })
        } else {
          this.setState({
            isAppEmpty: true,
          })
        }

      })





    // 积分任务获取
    dispatch(fetchUserCreditMission())
      .then((data) => {
        if (data.missions.length > 0) {
          document.documentElement.style.overflow = 'hidden';
          let strArr = []
          for (let i = 0; i < data.missions.length; i++) {
            strArr.push(data.missions[i].id)
          }
          const str = strArr.join(',')
          dispatch(updateUserCreditMission(str))
        }
        this.setState({
          mission: data.missions,
        })

        // 通知已经阅读
        dispatch(updateUserCreditMission())
      })
  }

  handleTab(index) {
    this.setState({
      pannelIdx: index,
    })

    if (index === 3) {
      this.props.dispatch(getBaseInfo())
    }
  }

  handleClose() {
    this.setState({
      courseTipOneClose: true,
      tokenHistoryClose: true,
      myInviteClose: true,
      tokenDepositClose: true,
    })
  }

  opentokenHistory() {
    this.props.dispatch(getCreditLog({
      page: 1,
      size: 10,
      api_token: getApiToken()
    }))
      .then((data) => {
        this.setState({
          creditLog: data.logs,
          creditLogTotal: data.total,
        })
      })
    this.setState({
      tokenHistoryClose: false,
    })
  }

  openMyInvite() {
    this.props.dispatch(fetchUserInvitees())
      .then(data => {
        this.setState({
          inviteList: data,
        })
      })

      // 更新baseInfo
      this.props.dispatch(getBaseInfo())


    this.setState({
      myInviteClose: false,
    })
  }

  openToeknDeposit() {
    this.setState({
      tokenDepositClose: false,
    })
  }

  changeToken(e) {
    this.setState({
      token: e.target.value,
    })
  }

  closeCredit() {
    const { mission } = this.state

    mission.pop();

    if (mission.length <= 0) {
      document.documentElement.style.overflow = 'scroll';
    }

    this.setState({
      mission,
    })

  }

  AddToken(val) {
    console.log(val)
    if (val) {
      this.setState((state) => {
        return {
          token: state.token + 100000,
        }
      })
    } else {
      this.setState((state) => {
        if (state.token - 100000 > 0) {
          return {
            token: state.token - 100000,
          }
        } else {
          return {
            token: 0
          }
        }

      })
    }
  }

  tokenConvert() {
    const { dispatch } = this.props;
    if (this.state.token >= 2000) {
      dispatch(fetchCreditEnchashmentGet(this.state.token))
        .then((data) => {
          this.props.dispatch(getBaseInfo());
          message.success("提现申请成功，请等待管理员审核")

        }, (code) => {
          if (code === "5000") {
            message.error("提现失败，没有足够的余额");
          }
        })
    } else {
      message.error("至少兑换2000积分")
    }

  }



  curImage() {
    const { processList } = this.state;
    return processList.length ? processList[this.state.processIdx].image : ""
  }

  curTitle() {
    const { processList } = this.state;
    return processList.length ? processList[this.state.processIdx].name : ""
  }

  curDesc() {
    const { processList } = this.state;
    return processList.length ? processList[this.state.processIdx].description : ""
  }

  changeCreditLogSize(n) {
    this.props.dispatch(getCreditLog({
      page: n,
      size: 10,
      api_token: getApiToken()
    }))
      .then((data) => {
        this.setState({
          creditLog: data.logs,
          creditLogTotal: data.total,
          creditLogCurPage: n,
        })
      })
  }

  render() {
    const {
      pannelIdx,
      processIdx,
      tokenHistoryClose,
      myInviteClose,
      tokenDepositClose,
      token,
      creditLog,
      inviteList,
      processList,
      creditLogTotal,
      creditLogCurPage,
      mission,
      isAppEmpty,
    } = this.state;

    const { userInfo, baseInfo } = this.props;


    return (
      <div className="tabMenuWrapper">
        {
          mission.length ? (<div key={index} className="creditMission" onClick={this.closeCredit}>
            <div class="creditBox">
              <img className="img-responsive" src={require('../../images/personal/ha.png')} alt="" />
              <div className="creditTitle">{mission[mission.length - 1].detail.name}成功</div>
              <div className="creditmark">获得{mission[mission.length - 1].detail.credit}积分</div>
            </div>
          </div>) : ""
        }

        <Row>
          {
            tabList.map((item, index) => (
              <Col md={3} key={index}>
                <div className={classNames("tabCard", { "active": (pannelIdx === index) })} onClick={this.handleTab.bind(this, index)}>{item}</div>
              </Col>
            ))
          }
        </Row>
        <Row>
          <Col md={12}>
            <div className={classNames("pannel", { "pannelActive": (pannelIdx === 0) })}>
              <Steps className="top" labelPlacement="vertical" current={processIdx}>
                {
                  processList.map((item, index) => {
                    return (<Steps.Step size="small" key={index} title={item.name} />)
                  })
                }

              </Steps>
              <Row>
                <Col md={10} mdOffset={1} >
                  <img className="img-responsive" src={this.curImage()} alt="" />
                  <p className="processTitle">
                    {this.curTitle()}
                  </p>
                  <p className="processDesc">
                    {
                      this.curDesc()
                    }
                  </p>
                </Col>
              </Row>
              {isAppEmpty ? (
                <Row>
                  <Col md={10} mdOffset={1} style={{ position: "relative" }}>
                    <img className="img-responsive" src={require("../../images/personal/0-14.png")} alt="" />
                    <p className="processTitle">
                      你尚未订购校萌升学服务
                    </p>
                    <p className="processDesc">
                      你可以扫描上方二维码，使用AI智能规划系统定制留学方案
                    </p>
                  </Col>
                </Row>
              ) : ""}
            </div>
            <div className={classNames("pannel", { "pannelActive": (pannelIdx === 1) })}>
              <Selfinfo />
            </div>
            <div className={classNames("pannel", { "pannelActive": (pannelIdx === 2) })}>
              <Products />
            </div>
            <div className={classNames("pannel", { "pannelActive": (pannelIdx === 3) })}>
              <div className="myToken">
                <div className="">
                  <div className="headline">我的积分</div>
                  <div className="tokenWrap myTokenWrap">
                    <div className="dataItem">
                      <p className="itemTitle">当前等级</p>
                      <p className="itemDate">{baseInfo.level}</p>
                    </div>
                    <div className="dataItem">
                      <p className="itemTitle">当前积分</p>
                      <p className="itemDate">{parseInt(baseInfo.credit)}</p>
                      <p className="itemMore" onClick={this.opentokenHistory}>历史记录</p>
                    </div>
                    <div className="dataItem">
                      <p className="itemTitle">我的邀请</p>
                      <p className="itemDate">{baseInfo.total_invite}</p>
                      <p className="itemMore" onClick={this.openMyInvite}>查看详情</p>
                    </div>
                    <div className="rules">
                      <PerfectScrollbar>
                        <h4>积分规则</h4>
                        <ul style={{ listStyle: "disc", paddingLeft: "0.5rem" }}>
                          {
                            this.state.ruleList.map((item, index) => {
                              return (<li key={index}>{item.name}后将获得{parseInt(item.credit)}积分</li>)
                            })
                          }
                        </ul>
                      </PerfectScrollbar>
                    </div>
                  </div>
                </div>
                <div>
                  <div className="headline">积分商城</div>
                  <div className="tokenWrap shop">
                    <Row>
                      <Col md={6}>
                        <div className="shopItem">
                          <img className="img-responsive" src={require('../../images/personal/deposit.png')} alt="" />
                          <div className="shopItemBtm">
                            <p>积分提现</p>
                            <button onClick={this.openToeknDeposit}>兑换</button>
                          </div>
                        </div>
                      </Col>
                      <Col md={6}>
                        <div>
                          <img className="img-responsive" src={require('../../images/personal/course01.png')} alt="" />
                          <div className="shopItemBtm">
                            <p>校萌游戏币兑换</p>
                            <button className="expect_btn">即将开放</button>
                          </div>
                        </div>
                      </Col>
                    </Row>
                  </div>
                </div>
                {/* 积分历史记录 */}
                <div className={classNames("shade", { "hidden": tokenHistoryClose })}>
                  <div className="tokenHistory">
                    <div className="title">积分历史记录</div>
                    <div className="main">
                      <div className="scrollWrap">
                        {
                          creditLog.length ?
                            (<table>
                              <tbody>
                                {
                                  creditLog.map((item, index) => {
                                    return (
                                      <tr key={index}>
                                        <td>{item.remark}</td>
                                        <td>{item.type ? "-" : "+"}{parseInt(item.total)}</td>
                                        <td>{item.created_at}</td>
                                      </tr>
                                    )
                                  })
                                }
                              </tbody>
                            </table>) : (<p>暂无数据!</p>)
                        }

                      </div>
                      <div className="paging">
                        <Pagination simple defaultCurrent={creditLogCurPage} total={creditLogTotal} onChange={this.changeCreditLogSize} />
                      </div>
                      <div className="closeWrap">
                        <button onClick={this.handleClose}>关闭</button>
                      </div>
                    </div>
                  </div>
                </div>

                {/* 我的邀请 */}
                <div className={classNames("shade", { "hidden": myInviteClose })}>
                  <div className="myInvite">
                    <div className="title">我的邀请</div>
                    <div className="main">
                      <div className="groupWrap">
                        <span>我的邀请码</span>
                        <input type="text" readOnly value={userInfo.invite_code} />
                      </div>
                      <div className="groupWrap">
                        <span>我邀请的好友</span>
                        <div className="inviteList">
                          <PerfectScrollbar>
                            {
                              inviteList.map((item, index) => {
                                return (
                                  <div key={index} className="inviteItem" title={item.nickname}>
                                    <img className="invite_avator" src={item.avatar} alt="" />
                                    <p className="invite_name">{item.nickname}</p>
                                  </div>
                                )
                              })
                            }
                          </PerfectScrollbar>
                        </div>
                      </div>
                      <p className="tips">您的好友填写您的邀请码后，您和您的好友都能获得积分奖励。</p>
                      <div className="closeWrap">
                        <button onClick={this.handleClose}>关闭</button>
                      </div>
                    </div>
                  </div>
                </div>

                {/* 积分兑换 */}
                <div className={classNames("shade", { "hidden": tokenDepositClose })}>
                  <div className="tokenDeposit">
                    <img className="img-responsive" src={require('../../images/personal/deposit.png')} alt="" />
                    <div className="main">
                      <p className="title">积分提现</p>
                      <p className="rule">100积分=0.8元人民币，至少兑换100000积分，通过审核后即可提现</p>
                      <div className="depositWrap">
                        <div className="depositTop">
                          <p className="strong">积分兑换</p>
                          <p>可用积分：{baseInfo.credit}</p>
                        </div>
                        <div className="depositBtm">
                          <div className="depositMonBtm">
                            <span className="left" onClick={this.AddToken.bind(this, 0)}>-</span>
                            <input type="text" readOnly value={accounting.formatMoney(token,"",0,",")} onChange={this.changeToken} />
                            <span className="right" onClick={this.AddToken.bind(this, 1)}>+</span>
                          </div>
                        </div>
                      </div>
                      <p className="note">可兑换{parseInt(token / 100 * 0.8)}人民币</p>
                      <div>
                        <button onClick={this.tokenConvert}>确认</button>
                        <button onClick={this.handleClose}>取消</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>


            </div>
          </Col>

        </Row>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    userInfo: state.userInfo,
    baseInfo: state.baseInfo,
  }
}


export default connect(mapStateToProps)(index)