import React, { Component } from 'react'
import NavBar from '../../Components/NavBar/NavBar';
import ScrollTop from '../../Components/ScrollTop/ScrollTop';
import { withRouter } from 'react-router-dom'
import './index.css';


class StrategyDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      idx: 0,
    }

    this.changeStyle = this.changeStyle.bind(this);
  }

  componentDidMount() {
    const d = decodeURI(this.props.location.hash);
    if(d === "#留学初识") {
      this.setState({
        idx: 0
      })
    } else if(d === "#申请流程") {
      this.setState({
        idx: 1
      })
    } else if(d === "#升学考试") {
      this.setState({
        idx: 2
      })
    } else if(d === "#签证雷区") {
      this.setState({
        idx: 3
      })
    } else if (d === "#学费自付")  {
      this.setState({
        idx: 4
      })
    } else if (d === "#行前准备") {
      this.setState({
        idx: 5
      })
    } else if(d === "#海外住宿") {
      this.setState({
        idx: 6
      })
    }

    var oversea = document.getElementById("海外住宿");
    var about = document.getElementById("留学初识");
    var application = document.getElementById("申请流程");
    var entra = document.getElementById("升学考试");
    var visa = document.getElementById("签证雷区");
    var fee = document.getElementById("学费自付");
    var trip = document.getElementById("行前准备");
    window.addEventListener('scroll', ()=> {
      var scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
      
      if(scrollTop > oversea.offsetTop) {
        this.setState({
          idx: 6,
        })
      } else if(scrollTop < (about.offsetTop+about.offsetHeight)) {
        this.setState({
          idx:0,
        }) 
      } else if( (scrollTop > application.offsetTop) && (scrollTop <( application.offsetTop + application.offsetHeight) ) ) {
        this.setState({
          idx:1,
        }) 
      } else if( (scrollTop > entra.offsetTop) && (scrollTop <( entra.offsetTop + entra.offsetHeight) ) ) {
        this.setState({
          idx:2,
        }) 
      } else if( (scrollTop > visa.offsetTop) && (scrollTop <( visa.offsetTop + visa.offsetHeight) ) ) {
        this.setState({
          idx:3,
        }) 
      } else if( (scrollTop > fee.offsetTop) && (scrollTop <( fee.offsetTop + fee.offsetHeight) ) ) {
        this.setState({
          idx:4,
        }) 
      } else if( (scrollTop > trip.offsetTop) && (scrollTop <( trip.offsetTop + trip.offsetHeight) ) ) {
        this.setState({
          idx:5,
        }) 
      }
    })


  }

  changeStyle(index) {
    this.setState({
      idx: index,
    })
  }

  render() {
    const lists = ['留学初识','申请流程','升学考试','签证雷区','学费自付','行前准备','海外住宿'];
    return (
      <div className="strategy-detail-wrapper row">
        <ScrollTop />
        <NavBar backgroundColor="#fff" boxShadow="0 0 10px #ccc" fixed="true"/>
        <div className="col-md-10 col-md-offset-1">
          <div className="strategy-detail-body row">
            <div className="nav-list" style={{padding: '0',position: 'fixed'}}>
              <ul className="category-list">
                {lists.map((item,index)=>
                  <li className={this.state.idx === index ? 'category-unit-active' : ''} key={index} onClick={this.changeStyle.bind(this,index)}><a href={'#'+ item}>{ item  }</a></li>
                )}
              </ul>
            </div>
            <div className="pull-right strategy-detail-content" style={{padding: '0'}}>
              <a id="留学初识">
                <div className="content-block">
                  <h2>留学初识</h2>
                  <ul className="qa-list">
                    <li className="qa-list-item">
                        <h3><i className="bulb-icon"></i>为什么选择英国？</h3>
                        <h4><span className="post-num">1</span>  较短学制，专业细分健全</h4>
                        <p>作为世界上大学最多的国家之一，英国有近二百所高等院校，提供超过37000种学位课程供学生选择就读，且专业类别分类齐全，教学结构严谨细致，针对性强，三年本科学制、一年硕士学制也让学生感到相对
                          轻松，减轻留学财务负担。
                        </p>
                        <h4><span className="post-num">2</span> 良好语言环境，更少的资格要求</h4>
                        <p>对于海外留学生，绝大部分英国大学都接受两年内有效的IELTS成绩，除极个别专业，想申请英国大学学制课程的学生只需要准备IELTS成绩作为语言材料便可，无需额外提供GMAT或GRE成绩，相对来说减轻留学生
                          的留学压力。另外，作为英语的发源地，选择英国进行学习与生活，都为学生提供一个锻炼优雅英音的语言环境。</p>
                        <h4> <span className="post-num">3</span>教育含金量高</h4>
                        <p>英国大学数量多，教育质量也高，在近年来QS排名上，英国的不少名校都在榜单上位于前列，在2019年的QS排名中，TOP100名校中有19所英国院校。G5、红砖大学、罗素集团等等，英国的大学在世界范围内都享有崇高的
                          声誉，无论是师资学风、教学质量还是学科研究都走在世界的前列，为学界先头部队。
                        </p>
                        <h4> <span className="post-num">4</span>边打工边学习</h4>
                        <p>在英国，留学生无需另行申请工作许可证，便可通过多种专门为留学生提供的合法打工途径来赚取生活费零花钱，留学生可以在学习之余进行兼职工作，每周上限20小时，最低时薪约为人民币75元。边打工边学习，在留学之余
                          进入当地社会充分体验人生与风土人情，也能够在经济上为自己的留学花销提供“支援”。
                        </p>
                    </li>
                  </ul>
                </div>
              </a>
              <a id="申请流程">
              <div className="content-block">
                <h2>申请流程</h2>
                <ul className="qa-list">
                  <li className="qa-list-item">
                      <h3><i className="bulb-icon"></i>读研基本材料</h3>
                      <div className="basicMaterial">
                        <div className="basicMateril-item">
                          <img className="img-responsive" src={`${require('../../images/process/grade.png')}`} alt=""/>
                          <div>学术证明材料</div>
                          <div>（成绩表）</div>
                        </div>
                        <div className="basicMateril-item">
                          <img className="img-responsive" src={`${require('../../images/process/ps.png')}`} alt=""/>
                          <div>个人陈述</div>
                          <div>（PS）</div>
                        </div>
                        <div className="basicMateril-item">
                          <img className="img-responsive" src={`${require('../../images/process/cv.png')}`} alt=""/>
                          <div>个人简历</div>
                          <div>（CV）</div>
                        </div>
                        <div className="basicMateril-item">
                          <img className="img-responsive" src={`${require('../../images/process/letter.png')}`} alt=""/>
                          <div>推荐信</div>
                          <div style={{height: "0.24rem"}}>{}</div>
                        </div>
                        <div className="basicMateril-item">
                          <img className="img-responsive" src={`${require('../../images/process/IELTS.png')}`} alt=""/>
                          <div>语言成绩</div>
                          <div style={{fontSize: "0.16rem"}}>（一般为IELTS）</div>
                        </div>
                        <div className="basicMateril-item">
                          <img className="img-responsive" src={`${require('../../images/process/material.png')}`} alt=""/>
                          <div>其他学校要求</div>
                          <div>提交的材料</div>
                        </div>
                        <div className="basicMateril-item">
                          <img className="img-responsive" src={`${require('../../images/process/passport.png')}`} alt=""/>
                          <div>护照</div>
                        </div>
                        <div className="basicMateril-item">
                          <img className="img-responsive" src={`${require('../../images/process/internship.png')}`} alt=""/>
                          <div>实习/工作证明</div>
                        </div>
                        <div className="basicMateril-item">
                          <img className="img-responsive" src={`${require('../../images/process/fee.png')}`} alt=""/>
                          <div>申请费用</div>
                        </div>
                      </div>
                      <h3><i className="bulb-icon"></i>开课时间</h3>
                      <p>大多数排名位居上游的英国名牌院校都是秋季开课（9月），申请需提前一年提交（部分学校约10月末开放申请），因为英国院校进行录取一般遵循先到先得的原则，名额有限，故越早准备好留学材料的同学，
                        便可以越早抢占先机。递交申请时语言成绩是可以暂时不上交的，英国院校一般会根据学术材料、PS、CV以及推荐信来考虑学生是否符合申请要求，若通过便会发放conditional offer,之后再由学生补充语言成绩
                        以及相关证明材料。</p>
                  </li>
                  <li className="qa-list-item">
                      <h3><i className="bulb-icon"></i>时间规划</h3>
                      <div className="time-plain">
                        <div className="time-plain-item">
                          <div className="mon-gup">7-9月</div>
                          <p>撰写CV、PS与推荐信，准备并核实申请材料，待学校开通通道后递交材料</p>
                        </div>
                        <div className="time-plain-item">
                          <div className="mon-gup">8-12月</div>
                          <p>学校审核，发放第一批次offer</p>
                        </div>
                        <div className="time-plain-item"> 
                          <div className="mon-gup">12-2月</div>
                          <p>第二轮审核+发放offer(有些招不满的专业还会继续招收第三轮)</p>
                        </div>
                        <div className="time-plain-item">
                          <div className="mon-gup">3-4月</div>
                          <p>语言考不出来的学生配语言课程（语言课名额有效，满员就关闭申请）</p>
                        </div>
                        <div className="time-plain-item">
                          <div className="mon-gup">5-8月</div>
                          <p>体检，办理签证，租房和订机票，做好一切行前准备</p>
                        </div>
                        <div className="time-plain-item">
                          <div className="mon-gup">9-10月</div>
                          <p>入读主课，读语言课的学生需要在英国进行续签</p>
                        </div>
                      </div>
                  </li>
                  <li className="qa-list-item">
                      <h3><i className="bulb-icon"></i>TIPS</h3>
                      <ul className="tips">
                        <li>
                          <span>1</span>
                          <p>申请英国院校一定要注意关注学校的申请deadline，因为英国院校各专业发放的offer有限，一旦招满学生便有可能提前关闭申请通道，便使得学生申请无门。</p>
                        </li>
                        <li>
                          <span>2</span>
                          <p>若收到了Conditional Offer (有条件录取信),但语言--成绩一直达不到专业，就要认真考虑申请就读语言班了（一般是5-6周），语言班的位置也极其有限，不要因为语言成绩不达标，也不报上
                            语言班而错失了就读理想学校的机会。
                          </p>
                        </li>
                        <li>
                          <span>3</span>
                          <p>
                            除却自身条件以外，文书（PS、CV）也是一个极其重要的申请加分项。一份好的文书能够将学生的个人能力经历以最优化的形式体现，招生官可以从文书当中了解到学生的学习情况、只是运用能力以及
                            理想和职业规划等，一份优秀的，具备闪光点的文书能使学生的申请过程更加顺利，更有可能受到学院专业审理人的青睐，更有机会就读dream school，故文书必须用心撰写、细致操刀。
                          </p>
                        </li>
                        <li>
                          <span>4</span>
                          <p>毕业的学生需要提供毕业证和学位证；已经工作的同学需要提供工作证明。</p>
                        </li>
                        <li>
                          <span>5</span>
                          <p>申请部分院校的文科专业需要提供writing sample，具体内容参照该专业的申请要求。</p>
                        </li>
                        <li>
                          <span>6</span>
                          <p>申请艺术专业的学生需要提供作品集</p>
                        </li>
                        <li>
                          <span>7</span>
                          <p>申请G5以及好的商学院，最好在初次投递材料的时候附有雅思和GMAT成绩。</p>
                        </li>
                      </ul>
                  </li>
                </ul>
              </div>
              </a>
              <a id="升学考试">
              <div className="content-block">
                <h2>升学考试</h2>
                <ul className="qa-list">
                  <li className="qa-list-item">
                      <h3><i className="bulb-icon"></i>雅思</h3>
                      <p>雅思考试（IELTS）,全称为国际英语测试系统（International English Language Testing System）,是著名的国际性英语标准化水平测试之一。雅思考试还是全球认可度最高的国际英语测试，获得
                        全球认可度最高的国际英语测试，获得全球超过140多个国家和地区的10,000所院校机构的认可，每年有超过300万人次参加雅思考试。
                      </p>
                      <p>注重"沟通为本"的雅思考试，是从听、说、读、写四方面进行英语能力全面考核的国际考试，能够立体综合地精准测评考生的英语语言运用能力。雅思考试报名为全年开放式，考试日期安排
                        公布后即可报名，最晚报名时间为考试前两个星期。考生可以根据自己的情况选择全国任何一个考点的考试，不受地域限制。雅思考试成绩为9分，有效期限通常为2年。
                      </p>
                      <h4>学术类（A类）考试结构及题型</h4>
                      <ul className="ques-type">
                        <li>
                          <h4><span className="post-num">1</span> 听力</h4>
                          <p>考试时长： 30分钟，并有10分钟时间将答案誊写到答题卡</p>
                          <p>考试题型： 共包含4段独白和对白。每段听力材料只播放一次，对应10道问题，问题的安排顺序和答案在听力材料中出现的顺序一致。</p>
                        </li>
                        <li>
                        <h4><span className="post-num">2</span> 阅读</h4>
                          <p>考试时长: 60分钟</p>
                          <p>考试题型： 共包含3段文章及相应40道题目。每一篇文章所需要回答的问题数量并不相同。文章内容和题目均出现在试卷中。</p>
                        </li>
                        <li>
                          <h4><span className="post-num">3</span> 写作</h4>
                          <p>考试时长： 60分钟</p>
                          <p>考试题型：共两篇作文。第一篇（Task 1）为图表分析写作，如表格、曲线图、柱状图或示意图等，考生需对这些信息或数据进行描述，要求不得少于150字；第二篇（Task 2）为议论性文章写作，考生需根据题目所给出的看法、问题或议题进行论述，要求不得少于250字。考官给以下四项分别评等级分：任务完成情况（Task 1）/任务回答情况（Task 2），连贯与衔接，词汇变换、句式多样性和语法准确性。这四方面所占权重相同。
                          </p>
                        </li>
                        <li>
                          <h4><span className="post-num">4</span> 口语</h4>
                          <p>考试时长： 11-14分钟</p>
                          <p>考试题型：雅思考试通过考生与考官之间进行一对一交流的形式对考生的英语口语水平进行考察，是目前最接近真实生活场景的互动式测评。所有的雅思考官会接受统一的培训与严格监控以确保其按照全球统一标准对考生进行评判，标准共分为四项：流利性与连贯性、词汇多样性、语法多样性及准确性、发音。每场考试都会全程录音以进行后期监测，保证口语考试的客观性和公平性。</p>
                        </li>
                      </ul>
                  </li>
                  <li className="qa-list-item">
                      <h3><i className="bulb-icon"></i>译赛</h3>
                      <p>UKiset（译赛）——UK Independent Schools Entry Test, 全称为英国私立学校入学测试。是一项自适应性在线入学考试，考试语言为英文。主要针对9岁半至18岁的学生。它以智能的方法考核学生的学习能力和发展潜能，是帮助识别候选人的优缺点和学习偏好的一种标准化测评。已有过百所的顶尖私立学校已将“译赛”作为筛选海外学生的一个重要标准之一。</p>
                      <p>Ukiset（译赛）由英国加比达斯、杜伦大学、剑桥英语参与试题设计并得到英国领事馆文化教育处对考试的全程监督。UKiset测试有效期为一年，学生可间隔半年再一次进行测试。</p>
                      <h4>考试内容</h4>
                      <ul className="exam-content">
                        <li>
                          <h4><span className="post-num">1</span>推理考试</h4>
                            <p>时长：45分钟</p> 
                            <p>整个推理考试由3个小部分组成</p> :
                            <p>a) 文字推理：文字推理包括了正义词、反义词、近义词等英语词汇类的测试。</p> 
                            <p>b) 数学：数学测试包含了一些适龄的基本数学计算题与解析题以此来测试学生的数学应用能力。</p>
                            <p>c) 非文字推理：这部分测试包括了逻辑思维和空间想象能力的测试，以此来评定学生的学习潜力。</p>
                          <p></p>
                        </li>
                        <li>
                          <h4><span className="post-num">2</span>剑桥英语考试</h4>
                          <p>时长：30分钟</p>
                          <p>剑桥英语主要测试学生听力、阅读和语法等英语运用能力。并将成绩按照最高C2，最低A1来进行划分。</p>
                        </li>
                        <li>
                          <h4><span className="post-num">3</span>创意写作</h4>
                          <p>时长：30分钟</p>
                          <p>按照考生的年龄不同给出不同的题目，考生需要在30分钟内完成 一篇150-300字的写作。创意写作部分将不会被评分，但是会与学生的另外两部分成绩一起递交给学校，学校以此来评定学生的写作能力和英语运用能力。</p>
                        </li>
                      </ul>
                      <p>在考试结束后3-5个工作日内，参加考试的学生会收到一份成绩报告单，此报告单将以电子邮件的形式发送给学生或注册时所留的邮箱地址；同时，UKiset会将另外一份成绩报告单发送到考生注册时所希望投递成绩的学校。</p>
                  </li>
                </ul>
              </div>
              </a>
              <a id="签证雷区">
              <div className="content-block">
                <h2>签证雷区</h2>
                <ul className="qa-list">
                  <li className="qa-list-item">
                      <h4><span className="post-num">1</span>移民倾向问题</h4>
                      <p>签证官首先会认为你带有移民倾向。但他们大多数时候是凭主观判断的，一旦你被怀疑了，拒签就是百分百的事情，但你对此还一脸茫然。因此，你一定要向签证官证明自己并没有所谓的移民倾向，证明自己国内的约束力不弱，滞留不归的可能性微乎其微。签证官一般通过这些问题来判断一个人有无移民倾向：出国目的明确吗？回国时间明确吗？家庭关系、在国内的约束条件如何？</p>
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">2</span>过往的拒签记录</h4>
                      <p>以前是否有过在境外滞留或拒签或拒绝入境的记录，无论是对于英国的申请还是对于其他非居住国家的申请；如果有这些记录可能会导致各类英国新递申请的拒签。</p>
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">3</span>没有去指定医院做肺结核体检</h4>
                      <p>学生去了非指定的医院做肺结核体检，导致签证被拒。</p> 
                      <p>附上指定体检医院链接：https://www.gov.uk/government/publications/tuberculosis-test-for-a-uk-visa-clinics-in-china/approved-tuberculosis-testing-clinics-in-china
                      </p>
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">4</span>资金没存够28天就去网上提交签证申请</h4>
                      <p>比较多学生因为这个原因导致拒签，很多申请人在资金还没存够28天就在网上递交签证和预约签证。</p> 
                      <p>申请人必须在担保金存满28天以后，才可以在网上递交签证申请并付款。是否存满28天，是按网上付款当天为准，并非去签证中心递交签证当天。</p>
                      <p>可以先去online填写页面需要的信息保存，等资金在银行存入的时间超过28天，才可以去online点“提交”，然后再交签证费预约签证时间。</p>
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">5</span>存款证明开具出一个月后才递交签证</h4>
                      <p>存款证明开具出来必须30天内递交签证，如超过30天就算过期，需要重新开具。</p> 
                      <p>如遇到好的使馆工作人员会当面告诉你存款证明已过期，可以开具一份新的补充到使馆；相反没有提醒直接递交可能就会被导致拒签。</p>
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">6</span>没有提供有效的出生证明</h4>
                      <p>未成年的学生在办理签证时必须提供由政府相关部门发出的出生证明，以证明父母同意学生出国留学，且愿意负担一切学习生活费用以及法律责任。由于未成年学生不具备经济能力，没有提供出生证明代表没有父母的财力支持，这种情况被拒签的机率就相当大了。</p> 
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">7</span>关键签证材料必须是原件</h4>
                      <p>由于雅思成绩单，毕业证，本科成绩单等材料在申请学校的时候已经被检查过原件了，
                        有一些同学就误解了在签证申请时只需要提交复印件就可以了，这个想法是大错特错的！
                        学校的申请是由学校的行政部门来负责的，而签证的审理是由移民局的签证官来负责的，这是两个完全不同的部门。
                        即使你的申请材料已经被学校检查过了，但是并不代表签证官就不需要了。所以，所有提交给签证中心的材料都必须是原件。</p> 
                  </li>
                </ul>
              </div>
              </a>
              <a id="学费自付">
              <div className="content-block">
                <h2>学费自付</h2>
                <ul className="qa-list">
                  <li className="qa-list-item">
                      <p>交学费是英国留学行前最重要的一环。在收到学校邮件说明学费缴纳等行前事项后，学生可通过以下几种推荐的方式进行学费缴付：</p>
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">1</span>信用卡/借记卡</h4>
                      <p>国内银行会根据金额收取2.7%-3.0%的手续费，操作较为简单，但由于手续费率较高而较为昂贵。</p>
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">2</span>自动扣款</h4>
                      <p>在英国当地开一个Direct Debit账户，约定自动扣款缴纳学费以及住宿费，到约定时间便会自动扣款，适用于分期付款，比较放心方便。</p>
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">3</span>电汇</h4>
                      <p>在国内办理一张卡，里面存好学费以及住宿费（英镑），在国内银行柜台或网银进行电汇，转账最多需要十个工作日到账，故需要预留时间（注意学费缴纳截止时间），转账过程中会产生手续费，故在转账时要注明产生的手续费用在汇款人账上扣除。</p>
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">4</span>汇票</h4>
                      <p>一般是在中行进行办理，先把人民币汇成英镑，可以在汇率低的时候进行办理，赴英时携带汇票（护照与汇票分开保存），存有学费的汇票直接交到学校收费处即可，若其他汇票需要解汇则要到当地银行开户解汇。</p>
                  </li>
                  <li className="qa-list-item">
                    <div style={{paddingBottom: "0.4rem"}}>
                      <h4>截止日期</h4>
                      <p>英国部分学校逾期缴纳学费可能会产生高额滞纳金，也可能会影响在学校平台上正式注册为学生，故学生要记得在正式开学报到前将学费缴纳到位。在学校官网的Tuition Fee Payment或How To Pay等页面中一般会注明缴纳学费截止时间与详细的支付方式信息。</p>
                    </div>
                    <p>
                    另外，有部分学校是允许学生分期付款的，一般分为三期，第一期为入校报到（最晚11月初），第二期为第二年的一月份，第三期为第二年的3月底，分期付款一般只能用Direct Debit或Credit Card进行支付，若逾期缴纳会产生滞纳金，所以若选择分期付款形式，需要定期查看银行卡确保里面有足够钱款。
                    </p>
                    <p>
                    为了方便海外学生在英国当地的生活，建议学生到达英国后到银行去开户，尽早办理避免撞上新生入学开户高峰期所造成的缓慢的审查周期。英国当地银行为吸引学生开户会给学生提供各种优惠政策，比如开户返现金或较高利率等，学生在当地可以办理的Student Account属于现金账户（Current Account）的一种，一般只有全日制学生（Full-time student）才可以申请。
                    </p>
                  </li>
                </ul>
              </div>
              </a>
              <a id="行前准备">
              <div className="content-block">
                <h2>行前准备</h2>
                <ul className="qa-list">
                  <li className="qa-list-item">
                      <h4><span className="post-num">1</span>体检</h4>
                      <p>获得学生签证后，需要到当地的出入境检验检疫局办理《国际旅行健康检查证明书》和《国际预防接种证书》，出入境时卫生官员会要求你出示。预防接种依据个人情况而定。常规预防接种项目：麻疹、风疹、腮腺炎和白喉、破伤风免疫力的证明。</p>
                  </li>
                  <li className="qa-list-item checklist">
                      <h4><span className="post-num">2</span>入境文件</h4>
                      <p>Ø 已签证的有效护照；</p>
                      <p>Ø 机票；</p> 
                      <p>Ø 体检证明；</p> 
                      <p>Ø CAS打印件；</p> 
                      <p>Ø 学位 /毕业证书正本（或加盖学校公章的副本或持有毕业证明、在读证明）；</p> 
                      <p>Ø 经济证明文件：汇票、旅行支票或电汇单据、经济担保信、奖学金证明（如果有的话）银行帐单或资金证明（复印件即可）；</p> 
                      <p>Ø 住宿地址及电话</p> 
                      <p>Ø 护照照片若干张</p>                        
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">3</span>货币准备</h4>
                      <div className="time-plain">
                        <div className="time-plain-item">
                          <div className="mon-gup">少量现金</div>
                          <p>以备坐大巴等不时之需。一般200 到 500 英镑就足够了</p>
                        </div>
                        <div className="time-plain-item">
                          <div className="mon-gup">信用卡</div>
                          <p>大多数国际信用卡 VISA卡和万事达卡在英国都是通用的，购物或提现都非常简便。（需要提前了解手续费）</p>
                        </div>
                        <div className="time-plain-item">
                          <div className="mon-gup">旅行支票</div>
                          <p>相比携带大额现金更加安全，在丢失或被窃情况可立即挂失</p>
                        </div>
                        <div className="time-plain-item">
                          <div className="mon-gup">电汇</div>
                          <p>将现金汇到国外的最快捷而又最昂贵的方式。如果在英国银行已经有了自己的帐号就只能采用这种办法</p>
                        </div>
                        <div className="time-plain-item">
                          <div className="mon-gup">汇票</div>
                          <p>手续费比较低，在银行顺利开户以后就可以取钱。国内将汇票寄往英国需要两周甚至更长的时间，最好随身携带前往英国</p>
                        </div>
                      </div>
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">4</span>预定机票</h4>
                      <p>在订机票之前，一定要先确认签证的有效期，并确认机票日期在签证有效期范围内，确保能够顺利入境。同时记得与航空公司确认免费托运行李限额。一般航空公司允许免费托运的行李在 30 公斤左右，购买学生票可能免费托运行李额会高一些。</p>
                  </li>
                  <li className="qa-list-item checklist">
                      <h4><span className="post-num">5</span>预定机票</h4>
                      <p>在订机票之前，一定要先确认签证的有效期，并确认机票日期在签证有效期范围内，确保能够顺利入境。同时记得与航空公司确认免费托运行李限额。一般航空公司允许免费托运的行李在 30 公斤左右，购买学生票可能免费托运行李额会高一些。</p>
                      <h4>行李携带清单</h4>
                      <p>Ø 入境所需文件</p>
                      <p>Ø 根据当地季节和气候带上必备的衣物。建议常备保暖衣物以及雨具。</p>  
                      <p>Ø 在国内常用的日用品及药品（若携带处方药或受限药品过境，必须出具医生的药品服用处方证明）</p>  
                      <p>Ø 必备的电子设备</p>  
                  </li>
                </ul>
              </div>
              </a>
              <a id="海外住宿">
              <div className="content-block">
                <h2>海外住宿</h2>
                <ul className="qa-list">
                  <li className="qa-list-item">
                      <p>基本上，在英国住宿方式有以下三种：</p>
                  </li>
                  <li className="qa-list-item">
                      <h4><span className="post-num">1</span>寄宿家庭（ homestay ）</h4>
                      <p>学生将租下其中一个房间，与房主或者他们的家人同住。学生可以通过学校申请或者自己寻找合适的homestay。申请时应仔细研究房主的要求，避免不必要的麻烦甚至经济损失。</p>
                      <p>优点：实地体验英国文化，充分了解英国普通家庭的日常生活、风俗习惯、节日氛围。</p>
                      <p>缺点：自由度较低；长期处于陪伴和照料之下，无法完全真正体验独立生活。</p>
                  </li>
                  <li className="qa-list-item checklist">
                      <h4><span className="post-num">2</span>学校宿舍（ student accommodation ）</h4>
                      <h4>学校宿舍大体分为两种：Catered和Self-catered</h4>
                      <p>Catered ：Catered会提供早餐和周末的主餐（午餐或者晚餐）。</p>
                      <p> Self-catered ：学生可以自己下厨做饭。厨房的设施共用，但是厨具，餐具等需要自备。</p>
                      <h4>
                      Self-catered一般又分为三小种：Standard（公用厨卫）、En-suite（自带卫生间，公用厨房）、Studio（自带厨卫）
                      </h4>
                      <p>Standard ：又叫 shared 或 singleroom ，配有单人床，书桌等生活用品。厨房和卫生间共用。这种宿舍房源最丰富价格也最实惠。</p> 
                      <p>Ensuite ：卧室里面有基本生活设施和有独立卫生间，而厨房是共用的。</p> 
                      <p>Studio ：单人间，通常配置双人床，有独立浴室和厨房。这种宿舍房源较少，一般需要提前申请，房租也是最贵的。</p> 
                      <div style={{paddingTop: "20px"}}>
                        <p>优点：很好地感受同龄人的生活、设施齐全、安全性强。</p>
                        <p>缺点：饭堂味道一般且品种单一、公用厨房厕所可能产生困扰。</p>
                      </div>
                  </li>
                  <li className="qa-list-item checklist">
                      <h4><span className="post-num">3</span>自己租房（ private renting ）</h4>
                      <p>自己租房可以自主选择位置、价格、室友，但相对来说风险性也较高。</p>
                      <h4>租房住宿需要注意以下几点：</h4>
                      <p>1. 入住时间。租房合同的过期时间需要特别注意，找新房时要注意入住时间的衔接。</p>
                      <p>2. 房租。租房时一定要问清楚房租包含的费用。</p>
                      <p>3. 安全检查。最好亲自检查检测证书确认房屋内的电器用品都英国测试。</p>
                      <p>4. 房屋检查。入住前确认房屋状况是否良好，暖气、卫浴等设施是否正常。</p>
                      <p>5. 租金。很多留学生都碰到过押金要不回来的状况，所以一定要了解清楚押金相关的法律规定。</p>               
                  </li>
                </ul>
              </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    )
  }
}


export default withRouter(StrategyDetail)