import axios from 'axios'
import { getApiToken,removeApiToken, removeUserinfo, removeToken } from './auth'

const service = axios.create({
  timeout: 5000,
})

service.interceptors.request.use((config) => {
  if(getApiToken()) {
    config.headers['Authorization'] = 'Bearer' + getApiToken()
  }

  return config;
},error => {
  console.log(error)
  Promise.reject()
})

// 处理判断状态码
service.interceptors.response.use(
  response => {
    // toekn过期
    if((response.status === 401&& response.data.code === "0002" ) || response.data.code === "1000") {
      removeApiToken();
      removeUserinfo();
      removeToken();
      window.location.reload();
    }

    
    return response
  },
  error => {
    removeApiToken();
    removeUserinfo();
    removeToken();
    window.location.reload();
    return Promise.reject(error)
  }
  )

export default service;
