import React, { Component } from 'react'
/* 多语言 */
import { t } from '../../untils/i18n'
import { NavLink } from 'react-router-dom';
import './index.scss';
import {connect} from 'react-redux'
import classNames from 'classnames'
import { createLanguage } from '../../actions'
import lockr from 'lockr'


class NavBar extends Component {

  render() {
    const {setLanguage, language, baseInfo} = this.props;
    
    return (
      <div className={classNames({
        "nav-bar-wrapper-fixed": this.props.fixed || false,
        "nav-bar-wrapper": true,
        })} style={{
        background:this.props.backgroundColor,
        boxShadow: this.props.boxShadow,
        paddingTop: this.props.paddingTop,
      }}>
        <div className="nav-bar col-md-10 col-md-offset-1">
          <a href="/">
            <img className="logo" src={`${require('../../images/logo.png')}`} alt=""/>
          </a>
          <div className="nav-bar-menu pull-right">
            <ul>
              <li><NavLink exact to='/' activeClassName="active">{t('home')}</NavLink></li>
              <li><NavLink to='/realize' activeClassName="active">{t('learnuk')}</NavLink></li>
              <li><NavLink to="/service" activeClassName="active">{t('service')}</NavLink></li>
              <li><NavLink to="/strategy" activeClassName="active">{t('guide')}</NavLink></li>
              <li><NavLink to="/aboutus" activeClassName="active">{t('aboutus')}</NavLink></li>
              <li className="lang"><span className={classNames({"langopacity": (language === 'zh')})} onClick={() => {setLanguage('zh')}}>简</span><div className="separator"></div><span className={classNames({"langopacity": (language === 'en')})} onClick={() => {setLanguage('en')}}>EN</span></li>  
              <li><NavLink to="/personal" activeClassName="active"><img className="avator" src={baseInfo.avatar ? baseInfo.avatar : require('../../images/personal/toux.png')} alt="avator" /></NavLink></li>
            </ul>
          </div>
        </div>
      </div>
    )
  }
}


const mapStateToProps = state => {
  return {
    language: state.language,
    baseInfo: state.baseInfo,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setLanguage: language=> {
      dispatch(createLanguage(language));
      lockr.set('language',language);
    }
    
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(NavBar)
