import { detailGet, listGet } from '../api/post' 

export const getPostDetail = (data) => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      detailGet(data)
      .then(res=>{
        const data = res.data;
        if(data.code === '0000') {
          resolve(data.data);
        }
      })
    }) 
  }
}


export const getPostList = (data) => {
  return dispatch => { 
    return new Promise((resolve, reject) => {
      listGet(data)
        .then(res => {
          const data = res.data;
          if(data.code === '0000') {
            resolve(data.data)
          }
        })
    })
  }
}