import React, { Component } from 'react'
import {
  Row,
  Col
} from 'react-bootstrap'
import NavBar from '../../Components/NavBar/NavBar'
import PerfectScrollbar from 'react-perfect-scrollbar';
import { connect } from 'react-redux';
import { fetchProductsFiles, fetchProductsDetail } from '../../actions/product';
import { getApiToken } from '../../untils/auth'

import './index.scss'


class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fileList: [],
      allId: "",
      apitoken: getApiToken(),
      description: ""
    }
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    // 获取用户详情
    this.props.dispatch(fetchProductsFiles(
      this.props.match.params.id,
    )).then(data => {
      const arr = []
      data.map((item) => {
        arr.push(item.id)
      })
      this.setState({
        fileList: data,
        allId: arr.join(',')
      })
    })
    this.props.dispatch(fetchProductsDetail(this.props.match.params.id))
      .then(data => {
        this.setState({
          description: data.description
        })
      })

  }






  render() {
    const { fileList } = this.state
    return (
      <div className="downloadWrap">
        <NavBar backgroundColor="#fff" boxShadow="0 0 10px #ccc" />
        <div className="downloadBody">
          <Row>
            <Col md={10} mdOffset={1}>
              <Row>
                <Col md={9}>
                  <div className="downloadList">
                    <div className="menuTop">
                      <h2>下载资源</h2>
                      <a href={'http://www.schoolingchain.com/api/products/file_download?token=' + getApiToken() + '&productFileId=' + this.state.allId}>
                        <div className="all_download">全部下载</div>
                      </a>
                    </div>
                    <div>
                      <table>
                        <thead>
                          <tr>
                            <th>文件名称</th>
                            <th>大小</th>
                            <th>修改时间</th>
                            <th></th>
                          </tr>
                        </thead>
                        <tbody>
                          {
                            fileList.map((item, index) => {
                              return (
                                <tr key={index}>
                                  <td><span>{item.name}</span></td>
                                  <td><span>{item.size}MB</span></td>
                                  <td><span>{item.updated_at}</span></td>
                                  <td>
                                    <a
                                      className="download" 
                                      href={'http://www.schoolingchain.com/api/products/file_download?token=' + getApiToken() + '&productFileId=' + item.id} >

                                    </a>
                                  </td>
                                </tr>
                              )
                            })

                          }
                        </tbody>
                      </table>
                    </div>
                  </div>

                </Col>
                <Col md={3}>
                  <div className="sideTop">
                    <h3>雅思提分培训</h3>
                    <p>课程提供：校萌教育链</p>
                  </div>
                  <div className="sideBtm">
                    <PerfectScrollbar>
                      <div dangerouslySetInnerHTML={{ __html: this.state.description }}></div>
                    </PerfectScrollbar>
                  </div>
                </Col>
              </Row>
            </Col>
          </Row>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {

  }
}

export default connect(mapStateToProps)(index)