import {
  getProductsList,
  orderListGet,
  productsDetailGet,
  productsFilesGet,
  productsFileDownload,
} from '../api/products'
import { getApiToken } from '../untils/auth'

export const ProductsListGet = (info) => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken(),
        size: 10,
        page: 1,
        ...info,
      }
      getProductsList(data)
        .then((res) => {
          const data = res.data;
          if (data.code === "0000") {
            resolve(data.data);
          }
        }, () => {
          console.log("error")
        })
    })
  }
}

// 获取购买教程 
export const fetchOrderList = () => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken(),
        size: 10,
        page: 1,
      }
      orderListGet(data).then((res) => {
        const data = res.data;
        if (data.code === "0000") {
          resolve(data.data);
        }
      }, () => {
        console.log("error")
      })
    })
  }
}

// 查看详情

export const fetchProductsDetail = (productId) => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken(),
        productId,
      };
      productsDetailGet(data).then(res => {
        const data = res.data;
        if (data.code === "0000") {
          resolve(data.data);
        }
      }, () => {
        console.log("error")
      })

    })
  }
}

// 获取课程文件
export const fetchProductsFiles = (productId) => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken(),
        productId,
      };
      productsFilesGet(data)
        .then(res => {
          const data = res.data;
          if (data.code === "0000") {
            resolve(data.data)
          } else {
            reject(data.code)
          }
        })
    })
  }
}


// 下载课程文件
export const fetchFileDownload = (productFileId) => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken(),
        productFileId,
      };
      productsFileDownload(data)
        .then(res => {
          let blob = new Blob([res.data], {type: "application/octet-stream"});
          let objectUrl = URL.createObjectURL(blob);
          window.location.href = objectUrl;
        })
    })
  }
}