import request from '../untils/request'

// 课程列表
export function getProductsList(params) {
  return request({
    url: '/api/products/list',
    method: "get",
    params,
  })
}


// 用户已购买的课程列表
export function orderListGet(params) {
  return request({
    url: '/api/user/orders/list',
    method: "get",
    params,
  })
}

// 课程详情
export function productsDetailGet(params) {
  return request({
    url: '/api/products/detail',
    method: "get",
    params,
  })
}

// 课程文件下载
export function productsFileDownload(params) {
  return request({
    url: '/api/products/file_download',
    method: "get",
    responseType: "arraybuffer",
    params,
  })
}

// 课程文件

export function productsFilesGet(params) {
  return request({
    url: '/api/products/files',
    method: "get",
    params,
  })
}


