import lockr from 'lockr'


const defaultStore = {
  language: lockr.get('language') || 'zh',
  userInfo: lockr.get('userInfo') || {},
  baseInfo: lockr.get('baseInfo') || {credit: 0},
}

const reducers = (state = defaultStore, action) => {
  switch (action.type) {
    case "SET_LANGUAGE":
      return {
        ...state,
        language: action.language,
      }
    case "SET_QRCODE":
      return {
        ...state,
        qrcode: action.qrcode,
      }
    case "USERINFO": 
      return {
        ...state,
        userInfo: action.userInfo,
      }
    case "BASEINFO": 
      return {
        ...state,
        baseInfo: action.baseinfo,
      }
    default:
      return state;
  }
}

export default reducers;