import React, { Component } from 'react'
import { connect } from 'react-redux'

import LearnUk from '../../Page/LearnUk/LearnUk'
import EntranceService from '../../Page/EntranceService/EntranceService';
import AbroadStrategy from '../../Page/AbroadStrategy/AbroadStrategy';
import StrategyDetail from '../../Page/StrategyDetail/StrategyDetail';
import AboutUs from '../../Page/AboutUs/AboutUs';
import Post from '../../Page/Post/Post';
import LenUkDetail from '../../Page/LenUkDetail/LenUkDetail';
import Personal from '../../Page/Personal'
import Download from '../../Page/DownLoad'
import FullpageWrapper from '../../FullpageWrapper';

/* React Compoment */
import { IntlProvider } from 'react-intl';
import { languages } from '../../translations';

/* 添加router */
import { BrowserRouter, Route } from 'react-router-dom';

/* 本地存储 */ 
import lockr from 'lockr';



