function App() {
  return (
    <FullpageWrapper  />
  )
}

export class index extends Component {


  render() {
    let language = lockr.get("language"); 
    if(!language) {
      language = this.props.language;
    }
    return (
      <IntlProvider locale={language} messages={languages[language]}>
        <BrowserRouter>
          <div>
            <Route exact path="/" component={App} />
            <Route exact path="/realize" component={LearnUk} />
            <Route path="/realize/bannerlist" component={LenUkDetail} />
            <Route exact path="/service" component={EntranceService} />
            <Route exact path="/strategy" component={AbroadStrategy} />
            <Route exact path="/strategy/strategydetail" component={StrategyDetail} />
            <Route exact path="/aboutus" component={AboutUs} />
            <Route exact path="/aboutus/post/:id" component={Post} />
            <Route exact path="/personal" component={Personal} />
            <Route exact path="/download/:id" component={Download} />
          </div>
        </BrowserRouter>
      </IntlProvider>
    )
  }
}

const mapStateToProps = (state) => ({
  language: state.language
})

const mapDispatchToProps = {

}

export default connect(mapStateToProps, mapDispatchToProps)(index)
