import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import NavBar from '../../Components/NavBar/NavBar'
import ScrollTop from '../../Components/ScrollTop/ScrollTop';
import { t } from '../../untils/i18n';
import { connect } from 'react-redux'
import { getPostList, getPostDetail } from '../../actions/post'
import { getApiToken } from '../../untils/auth'
import { Pagination } from 'antd';


import './index.scss';


class AboutUs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      essayClose: true,
      postList: [],
      dyncList: [],
      postCurPage: 1,
      postTotal: 0,

    }

    this.handleClose = this.handleClose.bind(this);
    this.openMoreEssay = this.openMoreEssay.bind(this);
    this.changePostSize = this.changePostSize.bind(this);
  }

  componentDidMount() {
    const { dispatch } = this.props;

    dispatch(getPostList({
      page: 1,
      size: 10,
      category: 11, //校萌动态
      api_token: getApiToken(),
    })).then((data) => {
      if (data.posts.length >= 3) {
        for (let i = 0; i < 3; i++) {
          dispatch(getPostDetail({
            postId: data.posts[i].id,
            category: 11,
            api_token: getApiToken()
          })).then(data => {
            this.setState((state) => {
              const n = [...state.dyncList, data]
              return {
                dyncList: n,
              }
            })
          })
        }
      }

    })

    // 查看已完成的积分列表

  }

  handleClose() {
    this.setState({
      essayClose: true,
    })
  }

  openMoreEssay() {

    this.setState(() => {
      this.changePostSize(1)
      return {
        essayClose: false,
      }
    })

  }

  openPostList(postId) {
    this.props.history.push(`/aboutus/post/${postId}`)
  }

  changePostSize(n) {
    this.props.dispatch(getPostList({
      page: n,
      size: 10,
      category: 11,
      api_token: getApiToken()
    })).then((data) => {
      this.setState({
        postList: data.posts,
        postCurPage: n,
        postTotal: data.pages,
      })
    })
  }



  render() {

    const { language } = this.props;
    const { essayClose, postList, dyncList, postCurPage, postTotal } = this.state;

    return (
      <div className="aboutus-wrapper">
        <ScrollTop />
        <NavBar backgroundColor="#fff" boxShadow="0 0 10px #ccc" fixed="true" />
        <div className="aboutus-banner">
          <div className="row" style={{ marginRight: '0' }}>
            <div className="col-md-10 col-md-offset-1">
              <div className="bannerbg-top">
                <div className="aboutus-banner-left col-md-6">
                  <h1 style={(language === 'en') ? { marginBottom: "0" } : {}}>{t("title")}</h1>
                  <p className="about-sub-title" style={(language === 'en') ? { fontSize: "0.24rem", whiteSpace: "nowrap", fontWeight: "bold" } : {}}>
                    {t("hope")}
                  </p>
                  <p className="aboutus-intro">
                    {t("intro")}
                  </p>
                </div>
                <div className="aboutus-banner-right col-md-6">
                  <img className="img-responsive" src={`${require('../../images/Careers.png')}`} alt="" />
                </div>
              </div>
              <div className="bannerbg-bottom">
                <div className="introduction">
                  <div className="intro-item">
                    <div className="intro-item-img ">
                      <img src={`${require('../../images/about/member.png')}`} alt="" />
                    </div>
                    <p>
                      {t('desc1')}
                    </p>

                  </div>
                  <div className="intro-item">
                    <div className="intro-item-img">
                      <img src={`${require('../../images/about/plane.png')}`} alt="" />
                    </div>
                    <p>
                      {t('desc2')}
                    </p>
                  </div>
                </div>


                <div className="xiaomengBlock">
                  <div className="col-md-4 block-content">
                    <div>
                      <h3>{t('block1')}</h3>
                      <p style={(language === 'en') ? { fontSize: "16px", lineHeight: "20px" } : {}}>
                        {t('block1-1')}
                      </p>
                    </div>
                    <div>
                      <h3>{t('block2')}</h3>
                      <p style={(language === 'en') ? { fontSize: "16px", lineHeight: "20px" } : {}}>
                        {t('block2-1')}
                      </p>
                    </div>
                  </div>
                  <div className="col-md-4">
                    <img style={{ width: "100%" }} className="img-responsive" src={`${require('../../images/about/feaTial.png')}`} alt="" />
                  </div>
                  <div className="col-md-4 block-content">
                    <div>
                      <h3>{t('block3')}</h3>
                      <p style={(language === 'en') ? { fontSize: "16px", lineHeight: "20px" } : {}}>
                        {
                          t('block3-1')
                        }
                      </p>
                    </div>
                    <div style={{ marginTop: "0.5rem" }}>
                      <h3>{t('block4')}</h3>
                      <p style={(language === 'en') ? { fontSize: "16px", lineHeight: "20px" } : {}}>
                        {t('block4-1')}
                      </p>
                    </div>
                  </div>
                </div>
              </div>


              {
                dyncList.length > 2 ? (
                  <div className="dynaInfo row">
                    <h2>{t('news')}</h2>
                    <div className="dnyNews col-md-10 col-md-offset-1">
                      <Link to={"/aboutus/post/" + dyncList[0].id} className="dnyNews-main" style={{ background: `url(${dyncList[0].thumb}) no-repeat`, backgroundSize: 'cover' }}>
                        <div className='main-title'>
                          {dyncList[0].title}<br />
                          <span className="main-date">{dyncList[0].created_at}</span>
                        </div>
                      </Link>
                      <div className="dnyNews-side">
                        {
                          dyncList[1] ? (
                            <Link className="dnyNews-side-item" to={"/aboutus/post/" + dyncList[1].id} style={{ background: `url(${dyncList[1].thumb}) no-repeat`, backgroundSize: '100%' }}>
                              <div className="side-item-title">
                                {dyncList[1] ? dyncList[1].title : ""}
                              </div>
                            </Link>
                          ) : ""

                        }
                        {
                          dyncList[2] ? (<Link className="dnyNews-side-item" to={"/aboutus/post/" + dyncList[2].id} style={{ background: `url(${dyncList[2].thumb}) no-repeat`, backgroundSize: '100%' }}>
                            <div className="side-item-title">
                              {dyncList[2].title}
                            </div>
                          </Link>) : ""
                        }

                      </div>
                      <div className="seeMore" onClick={this.openMoreEssay}>
                        查看更多
                      </div>
                    </div>
                  </div>
                ) : ""

              }


              <div className="team-intro row">
                <h2>{t('teamIntro')}</h2>
                <div className="founder-list  col-md-12 ">
                  <div className="founder">
                    <img className="img-circle" src={`${require('../../images/about/person_1.png')}`} alt="" />
                    <h3>{t('person1')}</h3>
                    <p className="subdesc">{t('person1-1')}</p>
                    <p>{t("person1-2")}</p>
                    <p>{t('person1-3')}</p>
                  </div>
                  <div className="founder">
                    <img className="img-circle" src={`${require('../../images/about/person_2.png')}`} alt="" />
                    <h3>{t('person2')}</h3>
                    <p className="subdesc">{t('person2-1')}</p>
                    <p>{t('person2-2')}</p>
                    <p>{t('person2-3')}</p>
                    <p>{t('person2-4')}</p>
                    <p>{t('person2-5')}</p>
                  </div>
                  <div className="founder">
                    <img className="img-circle" src={`${require('../../images/about/person_3.png')}`} alt="" />
                    <h3>{t('person3')}</h3>
                    <p className="subdesc">{t('person3-1')}</p>
                    <p>{t('person3-2')}</p>
                    <p>{t('person3-3')}</p>
                  </div>
                </div>
              </div>

              <div className="conus row">
                <h2>{t('contactUs')}</h2>
                <div className="conus-body col-md-10 col-md-offset-1">
                  <div className="conus-left">
                    <ul>
                      <li>
                        <div className="conus-detail">
                          <p className="conus-detail-title">{t('address')}</p>
                          <p className="conus-detail-content">{t('address1')}</p>
                        </div>
                      </li>
                      <li>
                        <div className="conus-detail">
                          <p className="conus-detail-title">{t('email')}</p>
                          <p className="conus-detail-content"><span>info@schoolingchain.com</span></p>
                        </div>
                      </li>
                      <li>
                        <div className="conus-detail">
                          <p className="conus-detail-title">{t('phone')}</p>
                          <p className="conus-detail-content"><span>0756-2225520</span></p>
                        </div>
                      </li>
                    </ul>
                  </div>
                  <div className="conus-right">
                    <div className="qrcode-imgs">
                      <img src={`${require('../../images/xiaochengxu.png')}`} alt="小程序" />
                      <p>{t('miniProgram')}</p>
                    </div>
                    <div className="qrcode-imgs">
                      <img src={`${require('../../images/gongzhonghao.png')}`} alt="公众号" />
                      <p>{t('officialAccount')}</p>
                    </div>
                  </div>
                </div>
              </div>

              <div className="cooPar row">
                <h2>{t('partner')}</h2>
                <div className="cooPartner-body col-md-10 col-md-offset-1">
                  <div className="rowl">
                    <div>
                      <img src={`${require('../../images/about/shinechain.png')}`} alt="" />
                    </div>
                    <div>
                      <img src={`${require('../../images/about/366.png')}`} alt="" />
                    </div>
                  </div>
                  <div className="rowl">
                    <div>
                      <img src={`${require('../../images/about/fullcircle.png')}`} alt="" />

                    </div>
                    <div>
                      <img src={`${require('../../images/about/loyaleuc.png')}`} alt="" />
                    </div>
                  </div>


                </div>
              </div>
            </div>
          </div>
        </div>
        {/* 关于校萌_文章列表 */}
        {essayClose ? "" : (
          <div className="shade">
            <div className="essay">
              <div className="head">
                校萌动态
            </div>
              <div className="main">
                <div className="essayList">
                  <table>
                    <tbody>
                      {postList.map((item, index) => {
                        return (
                          <tr onClick={this.openPostList.bind(this, item.id)} className="essayItem" key={index}>
                            <td className="essayTitle">{item.title}</td>
                            <td className="date">{item.created_at.substring(0, 10)}</td>
                          </tr>
                        )
                      })}
                    </tbody>
                  </table>
                </div>
                <div className="paging">
                  <Pagination style={{ float: "right" }} simple defaultCurrent={postCurPage} total={postTotal} onChange={this.changePostSize} />
                </div>
                <div className="closeWrap">
                  <button onClick={this.handleClose} className="close_btn">关闭</button>
                </div>
              </div>
            </div>
          </div>
        )}

      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    language: state.language,
  }
}


export default connect(mapStateToProps)(AboutUs)