import zh from "./zh.js";
import en from "./en.js";

import {addLocaleData} from 'react-intl';
import zhLocaleData from 'react-intl/locale-data/zh';
import enLocaleData from 'react-intl/locale-data/en';


addLocaleData([
  ...zhLocaleData,
  ...enLocaleData,
]);


export const languages = {
  'zh': zh,
  'en': en,
}