import { applicationsListGet, userApplicationListGet,userApplicationProgresRead,userCreditMissionGet, userCreditMissionPost } from '../api/application'
import { getApiToken } from '../untils/auth'

export const fetchApplicationsList = () => {
  return dispatch => {
    return new Promise((resolve,reject) => {
      const data  = {
        api_token: getApiToken()
      }
      applicationsListGet(data)
        .then(res => {
          const data = res.data;
          resolve(data)
        })
    })

  }
}


export const fetchUserApplicationsList = () => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken()
      }
      
      userApplicationListGet(data)
        .then(res => {
          const { data } = res;
          if(data.code === "0000") {
            resolve(data.data);
          }
        })

    })
  }
}

export const fetchUserApplicationProgresRead = (id) => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken(),
        progresId: id,
      }

      userApplicationProgresRead(data)
        .then(res => {
          const { data } = res;
          if(data.code === "0000") {
            resolve(data.data)
          }
        })
    })
  }
}


export const fetchUserCreditMission  = ()  => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken(),
        page: 1,
        status: 0,
      }
      userCreditMissionGet(data)
        .then(res => {
          const {data} = res;
          if(data.code === '0000') {
            resolve(data.data);
          }
        })

    })
  }
}


export const updateUserCreditMission  = (id)  => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      const data = {
        api_token: getApiToken(),
        missionId: id,
      }
      userCreditMissionPost(data)
        .then(res => {
          const {data} = res;
          if(data.code === '0000') {
            resolve(data.data);
          }
        })

    })
  }
}
