import React, { Component } from 'react'
import NavBar from '../../Components/NavBar/NavBar'
import {
  Row,
  Col,
} from 'react-bootstrap'
import TabMenu from '../../Components/TabMenu'
import { connect } from 'react-redux'
import { getApiToken, removeToken, removeApiToken  } from '../../untils/auth'
import { getQrcode, Logout  } from '../../actions/login'

import './index.scss'
import { message } from 'antd';


class index extends Component {

  componentDidMount() {
    let params = new URLSearchParams(this.props.location.search);
    if (!getApiToken()) { 
      this.props.dispatch(getQrcode(params.get("inviter")))
    }
  }

  layout() {
    const {dispatch} = this.props;
    dispatch(Logout())
      .then(() =>{
        message.success("登出成功，正在跳转")
        removeToken();
        removeApiToken();
        setTimeout(function() {
          window.location.reload()
        }.bind(this), 1.5 * 1000)
      },() => {
        message.error("发生错误")
      })
  }


  render() {
    const { qrcode, userInfo, baseInfo } = this.props;
    return (
      <div className="personal-wrapper">
        <NavBar backgroundColor="#fff" boxShadow="0 0 10px #ccc" />
        {getApiToken() ? (
          <div>
            {/* 顶部 */}
            <div className="containerWrap">
              <Row>
                <Col md={4}>
                  <div className="account">
                    <div className="accountLeft">
                      <img className="img-responsive" src={baseInfo.avatar || require('../../images/personal/avator.png')} alt="avator" />
                    </div>
                    <div className="accountRight">
                      <p className="name">{userInfo.nickname}</p>
                      <p className="iden">校萌注册用户</p>
                      <p className="layout" onClick={this.layout.bind(this)}> 退出登录</p> 
                    </div>
                  </div>
                </Col>
                <Col md={8}>
                  <div className="detail">
                    <Row>
                      <Col md={6}>
                        <div className="token">
                          <p className="tokenTitle">我的积分</p>
                          <div className="toeknNum">{ parseInt(baseInfo.credit) }</div>
                        </div>
                      </Col>
                      <Col md={6}>
                        <div className="invite">
                          <p className="inviteTitle">我的邀请</p>
                          <div className="inviteNum">{  baseInfo.total_invite  }</div>
                        </div>
                      </Col>
                    </Row>
                  </div>
                </Col>
              </Row>
            </div>
            {/* 选项卡 */}
            <div className="containerWrap" style={{ paddingBottom: "100px" }}>
              <TabMenu />
            </div>
            {/* 获得积分时弹出提醒 */}
          </div>
        ) : (
            <div className="qrcode_wrap">
              {/* <img src={demo} alt=""/> */}
              {qrcode ? (
                <div>
                  <img src={qrcode} alt="" />
                  <div className="qr_text">请使用微信扫码登录</div>
                </div>
              ) : 
              <div>
                <div className="loading"></div>
                <p>正在获取二维码</p>
              </div>}
            </div>
          )}



      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    qrcode: state.qrcode,
    userInfo: state.userInfo,
    baseInfo: state.baseInfo,
  }
}

export default connect(mapStateToProps)(index)
