import { doLoginGet, doLoginPost, logout } from '../api/login';
import { setToken, getToken, setApiToken, setUserInfo, removeToken } from '../untils/auth'

// action creater
export const setQrCodeCreater = (qrcode) => {
  return {
    type: 'SET_QRCODE',
    qrcode: qrcode,
  }
}

export const UserInfoCreater = (info) => {
  return {
    type: 'USERINFO',
    userInfo: info
  }
}

// action creter

let poTimer;
let timer;
export const getQrcode = (inviter) => {
  return dispatch => {
    doLoginGet()
      .then((res) => {
        const data = res.data;
        let expires_in = data.data.expires_in * 1000;
        timer = setInterval(() => {
          const curTime = new Date().valueOf();
          if ((expires_in - curTime) < 1000 * 30) {
            doLoginGet().then(res => {
              if(data.code === "0000") {
                dispatch(setQrCodeCreater(res.data.data.qrcode))      
                expires_in = res.data.data.expires_in * 1000;
              }
            })
          }
        }, 1000 * 60)
        if (data.code === "0000") {
          setToken(data.data.token);
          dispatch(setQrCodeCreater(data.data.qrcode))
          poTimer = setInterval(() => {
            dispatch(login(inviter))
          }, 2 * 1000);
        } else {
          dispatch(data);
        }
      }, (data) => {
        // dispatch(data)
      })
  }
}

export const login = (inviter) => {
  let data;
  if(inviter) {
    data = {
      token: getToken(),
      inviter,
    }
  } else {
    data = {
      token: getToken(),
    }
  }
  
  return dispatch => {
    doLoginPost(data)
      .then((res) => {
        const data = res.data;
        if (data.code === "0000") {
          setUserInfo(data.data.userInfo)
          setApiToken(data.data.token);
          dispatch(setQrCodeCreater(''))
          dispatch(UserInfoCreater(data.data.userInfo))
          removeToken();
          clearInterval(poTimer)
          clearInterval(timer)
        } else if (data.code === "1000") {
          // 清除token重新登录
          removeToken()
        }
      })
  }
}


export const Logout = () => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      logout().then(res => {
        console.log(res)
        const { data } = res;
        if(data.code === "0000") {
          resolve()
        } else {
          reject()
        }
      })
    })
  }
}